import htmlTemplate from './CDashboardView.html';
import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';
/**
 * CDashboardView View的主Class定义
 * */
class CDashboardView extends CView{
    constructor(hParent){
        // supper call before user 'this' pointer.
        super();
        var _this = this;
        //1. internal variant / function
        _this.Data = {
            dsvLocTimeVal: 'n/a' ,dsvDurationTimeVal: 'n/a',dsvHwMacVal: 'n/a',dsvHwIPVal: 'n/a',dsvHwSubmaskVal: 'n/a',dsvIMEIVal: 'n/a',
            dsvFWVerVal: 'n/a',dsvModTblZConnectVal: 'n/a',dsvLteZSignalVal: 'n/a',dsvModTbl4GConnectVal: 'n/a',dsvLte4GSignalVal: 'n/a',
            dsvModTblGnssConnectVal: 'n/a',dsvGnssSignalVal: 'n/a',
        }
        // 多语言实现 => 移到Class最后避免影响阅读
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
                aParent: hParent,
                aNodeID: 'dashboardView',
                aHtml: htmlTemplate, 
                aRegCtrlCallBack: _this.regCtrlCb,
                aLanguage: { language: 'cn', 
                            htmlMap: {'cn': _this.cnHtmlMap ,'en': _this.enHtmlMap,},
                            jsMap: {'cn': _this.cnJsMap,'en': _this.enJsMap,} },
                aRenderData: _this.Data,
                aChildViews: [],
                aEvCallback: {evActived: _this.evMyActived,},
            };
        // init this class-object
        _this.init(option);
        //2. exported variant / function
    }
    //
    regCtrlCb(){
        var enJsMap = this.enJsMap;
        var cnJsMap = this.cnJsMap;
        //LTE-Z 连接网络BTN
        $('#dsvmtZConnectBtn').click(() => {
            let str = 
            {
                "command":"operateModul",
                "dat": {
                    "modulType": "LTE-Z",
                    "operation": "on"
                  }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    /* 返回参数格式
                        { "rc": 0/1, "errCode": "xxx"}
                    */
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'dsvOptSucc') + " code: " + data.errCode);
                    }
                    else{
                        yutls.msgBox(res);
                    }
                }
            });
        });
        //LTE-Z dis连接网络BTN
        $('#dsvmtZDisconBtn').click(() => {
            let str = 
            {
                "command":"operateModul",
                "dat": {
                    "modulType": "LTE-Z",
                    "operation": "off"
                    }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    /* 返回参数格式
                        { "rc": 0/1, "errCode": "xxx"}
                    */
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'dsvOptSucc') + " code: " + data.errCode);
                    }
                    else{
                        yutls.msgBox(res);
                    }
                }
            });
        });
        //LTE-4G 连接网络BTN
        $('#dsvmt4GConnectBtn').click(() => {
            let str = 
            {
                "command":"operateModul",
                "dat": {
                    "modulType": "LTE-4G",
                    "operation": "on"
                  }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    /* 返回参数格式
                        { "rc": 0/1, "errCode": "xxx"}
                    */
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'dsvOptSucc') + " code: " + data.errCode);
                    }
                    else{
                        yutls.msgBox(res);
                    }
                }
            });
        });
        //LTE-4G dis连接网络BTN
        $('#dsvmt4GDisconBtn').click(() => {
            let str = 
            {
                "command":"operateModul",
                "dat": {
                    "modulType": "LTE-4G",
                    "operation": "off"
                    }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    /* 返回参数格式
                        { "rc": 0/1, "errCode": "xxx"}
                    */
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'dsvOptSucc') + " code: " + data.errCode);
                    }
                    else{
                        yutls.msgBox(res);
                    }
                }
            });
        }); 
    }
    // evActived
    evMyActived(self) {
        self.Data.dsvLocTimeVal = theApp.m_hMainWnd.oStore.system.localDatetime;
        self.Data.dsvDurationTimeVal = theApp.m_hMainWnd.oStore.system.currDuration;
        self.Data.dsvHwMacVal = theApp.m_hMainWnd.oStore.system.hwMAC;
        //self.Data.dsvHwIPVal = theApp.m_hMainWnd.oStore.LAN.LAN[0].IP;
        self.Data.dsvHwIPVal = theApp.m_hMainWnd.oStore.system.hostIP.IP;
        self.Data.dsvHwSubmaskVal = theApp.m_hMainWnd.oStore.system.hostIP.subMask;
        self.Data.dsvIMEIVal = theApp.m_hMainWnd.oStore.system.hwIMEI;
        self.Data.dsvFWVerVal = theApp.m_hMainWnd.oStore.system.swVersion;
        //
        self.Data.dsvModTblZConnectVal = theApp.m_hMainWnd.oStore.LTEZ.connection;
        self.Data.dsvLteZSignalVal = theApp.m_hMainWnd.oStore.LTEZ.signal;
        self.Data.dsvModTbl4GConnectVal = theApp.m_hMainWnd.oStore.LTE4G.connection;
        self.Data.dsvLte4GSignalVal = theApp.m_hMainWnd.oStore.LTE4G.signal;
        self.Data.dsvModTblGnssConnectVal = theApp.m_hMainWnd.oStore.GNSS.connection;
        self.Data.dsvGnssSignalVal = theApp.m_hMainWnd.oStore.GNSS.signal;
    }
}
// 多语言实现
CDashboardView.prototype.enHtmlMap = {dsvSysTitleTxt: "System Status", dsvLocTimeTxt: "Local Time", dsvDurationTimeTxt:"Duration Time",dsvHwMacTxt:"Mac Address",dsvHwIPTxt:"IP Address",dsvHwSubmaskTxt:"Submask",dsvIMEITxt: "IMEI",dsvFWVerTxt:"Firmware Ver.",
        dsvModTitleTxt:"Module Status",dsvModTblTxt:"Module",dsvConnTblTxt:"Connection", dsvSignalTxt:"Signal(dBm)", dsvOperaTxt:"Operation",dsvmtZConnectBtn:"Connect",dsvmtZDisconBtn:"Disconnect",dsvmt4GConnectBtn:"Connect",dsvmt4GDisconBtn:"Disconnect",
}
CDashboardView.prototype.cnHtmlMap = {dsvSysTitleTxt:"系统状态",dsvLocTimeTxt:"本机时间",dsvDurationTimeTxt:"本次开机持续时间",dsvHwMacTxt:"本机硬件地址",dsvHwIPTxt:"本机IP地址",dsvHwSubmaskTxt:"子网掩码",dsvIMEITxt:"本机设备号",dsvFWVerTxt:"固件版本号",
        dsvModTitleTxt:"模块状态",dsvModTblTxt:"模块",dsvConnTblTxt:"连接状态", dsvSignalTxt:"信号强度(dBm)", dsvOperaTxt:"操作",dsvmtZConnectBtn:"连接",dsvmtZDisconBtn:"断开",dsvmt4GConnectBtn:"连接",dsvmt4GDisconBtn:"断开",
}
CDashboardView.prototype.enJsMap = {
    dsvOptSucc:"Command is successful！",
}
CDashboardView.prototype.cnJsMap = {
    dsvOptSucc:"操作成功！",
}

export default CDashboardView;