import htmlTemplate from './CGnssView.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CGnssView extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'ssvTabGnss',
            aHtml: htmlTemplate, //'./src/views/CSysStatusView/subviews/CGnssView.html',
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        //注册回调
    }
    // evActived
    myActived(self) {
        self.Data.ssvTabGnssGPSVal = "Long:" + theApp.m_hMainWnd.oStore.GNSS.GPS.longitude + " <> Lati:" + theApp.m_hMainWnd.oStore.GNSS.GPS.latitude;
        //
        self.Data.ssvTabGnssSatNumVal = theApp.m_hMainWnd.oStore.GNSS.satelliteNum;
        self.Data.ssvTabGnssSendNumVal = theApp.m_hMainWnd.oStore.GNSS.totalMsg;
        self.Data.ssvTabGnssSuccNumVal = theApp.m_hMainWnd.oStore.GNSS.succMsg;
        self.Data.ssvTabGnssFailNumVal = theApp.m_hMainWnd.oStore.GNSS.failMsg;
        self.Data.ssvTabGnssDstSimVal = theApp.m_hMainWnd.oStore.GNSS.targetSim;
        self.Data.ssvTabGnssLocSimVal = theApp.m_hMainWnd.oStore.GNSS.localSim;
        //
        self.Data.ssvtgnssBeamInfor1Val = theApp.m_hMainWnd.oStore.GNSS.beam1;
        self.Data.ssvtgnssBeamInfor2Val = theApp.m_hMainWnd.oStore.GNSS.beam2;
        self.Data.ssvtgnssBeamInfor3Val = theApp.m_hMainWnd.oStore.GNSS.beam3;
        self.Data.ssvtgnssBeamInfor4Val = theApp.m_hMainWnd.oStore.GNSS.beam4;
        self.Data.ssvtgnssBeamInfor5Val = theApp.m_hMainWnd.oStore.GNSS.beam5;
        self.Data.ssvtgnssBeamInfor6Val = theApp.m_hMainWnd.oStore.GNSS.beam6;
    }
}
CGnssView.prototype.Data = {
    ssvTabGnssSatNumVal:'n/a',ssvTabGnssSendNumVal:'n/a',ssvTabGnssSuccNumVal:'n/a',ssvTabGnssFailNumVal:'n/a',ssvTabGnssDstSimVal:'n/a',ssvTabGnssLocSimVal:'n/a',
    ssvtgnssBeamInfor1Val:'n/a',ssvtgnssBeamInfor2Val:'n/a',ssvtgnssBeamInfor3Val:'n/a',ssvtgnssBeamInfor4Val:'n/a',ssvtgnssBeamInfor5Val:'n/a',ssvtgnssBeamInfor6Val:'n/a',
    ssvTabGnssGPSVal:'n/a',
}
CGnssView.prototype.enHtmlMap = {ssvtgnssInforTitle:"GNSS Information",ssvTabGnssSatNum:"Satellites",ssvTabGnssSendNum:"Send",ssvTabGnssSuccNum:"Succ",ssvTabGnssFailNum:"Fail",ssvTabGnssDstSim:"Dst. SIM",ssvTabGnssLocSim:"Loc SIM",
    ssvtgnssBeamInfor1Txt:"Beam1",ssvtgnssBeamInfor2Txt:"Beam2",ssvtgnssBeamInfor3Txt:"Beam3",ssvtgnssBeamInfor4Txt:"Beam4",ssvtgnssBeamInfor5Txt:"Beam5",ssvtgnssBeamInfor6Txt:"Beam6",
    ssvTabGnssGPS: "GPS",
};
CGnssView.prototype.cnHtmlMap = {ssvtgnssInforTitle:"GNSS 信息",ssvTabGnssSatNum:"卫星数量",ssvTabGnssSendNum:"发送总条数",ssvTabGnssSuccNum:"发送成功次数",ssvTabGnssFailNum:"发送失败数",ssvTabGnssDstSim:"目标SIM卡号",ssvTabGnssLocSim:"本机SIM卡号",
    ssvtgnssBeamInfor1Txt:"波束1",ssvtgnssBeamInfor2Txt:"波束2",ssvtgnssBeamInfor3Txt:"波束3",ssvtgnssBeamInfor4Txt:"波束4",ssvtgnssBeamInfor5Txt:"波束5",ssvtgnssBeamInfor6Txt:"波束6",
    ssvTabGnssGPS: "GPS坐标",
};
CGnssView.prototype.enJsMap = {};
CGnssView.prototype.cnJsMap = {};

export default CGnssView;