import htmlTemplate from './CNetvTabRouting.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CNetvTabRouting extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        _this._netvTabRoutingTextareaVal = "No.  Dst.Net       SubMask    Gateway       Interface     Metric" + " \n ";
        _this.Data = {
            // netvTabRoutingTextareaVal: _this._netvTabRoutingTextareaVal,
        };
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'netvTabRouting',
            aHtml: htmlTemplate, //'./src/views/CSysStatusView/subviews/CNetvTabRouting.html',
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        var _this = this;
        //注册回调
        // netvTabRoutingRefreshBtn
        $('#netvTabRoutingRefreshBtn').click(()=>{
            // "dat": {
            //     "modulType": "LTE-Z/LTE-4G"
            //   }
            var str = {
                "command":"getRoutingInfor",
                "dat":{
                    "modulType":  $('#netvTabRoutingModuleSelector').children('option:selected').val()
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    //   "dat": {
                    //     "routing":[
                    //         {"dstNet":"xx", "subMask":"xx", "gwIP":"xx", "ifName":"xx", "Metric":"xx"},
                    //         {..} ]  }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        let text = this._netvTabRoutingTextareaVal;
                        data.dat.routing.forEach((value,index)=>{
                            let cnt = index + 1;
                            let line = cnt + '    ' + value.dstNet + '    ' + value.subMask + '    ' + value.gwIP + '    ' + value.ifName + '    ' + value.Metric + " \n ";
                            text = text + line;
                        });
                        // this.Data.netvTabRoutingTextarea = text;
                        $('#netvTabRoutingTextarea').text(text);
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        // routing operation
        function routingOperation(action){
            var dstNet = $('#netvTabRoutingInputDstNet').val();
            var subMask = $('#netvTabRoutingInputSubmask').val();
            var gwIP = $('#netvTabRoutingInputGW').val();
            var ifName = $('#netvTabRoutingInputIf').val();
            var Metric = $('#netvTabRoutingInputMetric').val();
            var parametersErr = '';
            if((undefined == dstNet)||(undefined == subMask)||(undefined == gwIP)||(undefined == ifName)||(undefined == Metric)
                ||('' == dstNet)||('' == subMask)||('' == gwIP)||(false == yutls.chkIpAddress(gwIP))||('' == ifName)||('' == Metric)){
                // alert("WLAN Port不能为空!");
                parametersErr = yutls.jsSwitchLang(_this._oLanguage, 'nvAbortErr1')+action+yutls.jsSwitchLang(_this._oLanguage, 'nvAbortErr2');//"取值无效,放弃["+action+"]操作！";
                alert(parametersErr);
                return;
            }
            // "dat": {
            //     "operation": "add/modify/del",
            //     "routingData": {"dstIP":"xx", "subMask":"xx", "gwIP":"xx", "ifName":"xx", "Metric":"xx"}
            var str = {
                "command":"setRouting",
                "dat":{
                    "operation": action,
                    "routingData": {
                        "dstNet": dstNet,
                        "subMask": subMask,
                        "gwIP": gwIP,
                        "ifName": ifName,
                        "Metric": Metric
                    }
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        yutls.msgBox(yutls.jsSwitchLang(_this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        }
        $('#netvTabRoutingAddBtn').click(()=>{
            routingOperation('add');
        });
        $('#netvTabRoutingDelBtn').click(()=>{
            routingOperation('del');
        });
        /* removed: 20190521, it's difficult to locate the data line and modify it in the table.
        $('#netvTabRoutingChgBtn').click(()=>{
            routingOperation('modify');
        });
        */

    }
    // evActived
    myActived(self) {
        let _this = self;
        $('#netvTabRoutingTextarea').text(_this._netvTabRoutingTextareaVal);
        //netvTabRoutingRefreshBtn 触发刷新！
    }
}
CNetvTabRouting.prototype.enHtmlMap = {
    netvTabRoutingM:"Module:", netvTabRoutingRefreshBtn:"Refresh", netvTabRoutingAddBtn:"ADD",netvTabRoutingDelBtn:"DEL",//netvTabRoutingChgBtn:"CHG",
};
CNetvTabRouting.prototype.cnHtmlMap = {
    netvTabRoutingM:"模块: ", netvTabRoutingRefreshBtn:"刷新", netvTabRoutingAddBtn:"新加",netvTabRoutingDelBtn:"删除",//netvTabRoutingChgBtn:"修改",
};
CNetvTabRouting.prototype.enJsMap = {
    operationSucc:'Operation is successful.',
    nvAbortErr1:"Incorrect value,abort[",nvAbortErr2:"] operation!",
};
CNetvTabRouting.prototype.cnJsMap = {
    operationSucc:'操作成功.',
    nvAbortErr1:"取值无效,放弃[",nvAbortErr2:"]操作！",
};


export default CNetvTabRouting;