/** * 全局的Store类定义 */
import $ from 'jquery';
import {yutls, } from 'yut';

class CStore {
    constructor() {
        // 构造函数
        var _this = this;
        //1.内部变量+内部函数
        //3.初始化对象和成员函数
        _this.defaultUser = {
            user : 'admin',
            pwd : 'admin'
        };
        _this.rtmp =  {
            "defaultRtmpSrvPortNum": 1935,
            "defaultRtmpSrvAppName": "live"
        };
        _this.system = ''; //data.dat.system;
        _this.LTEZ = ''; //data.dat.LTEZ;
        _this.LTE4G = ''; //data.dat.LTE4G;
        _this.GNSS = ''; //data.dat.GNSS;
        _this.DHCP = ''; //data.dat.DHCP;
        _this.LAN = ''; //data.dat.LAN;
        _this.RTMP = ''; //data.dat.RTMP;
        _this.VPN = ''; //data.dat.VPN;
        _this.FireWall = ''; //data.dat.FireWall;
        _this.Mapping = ''; //data.dat.Mapping;
        _this.NTP = ''; //data.dat.NTP;

        // get this global var
        _this.render();
    }
    render() {
        this.getHostStatusInfo();
    }
    getSysInfor(){
        var str = {
            "command":"getSysInfor"
        }
        parameters = JSON.stringify(str);
        $.ajax({
            type:"POST",
            url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
            data:parameters,
            contentType: 'application/json',
            dataType: 'json',
            success: (res)=>{
                //var data = JSON.parse(res);
                var data = res;
                console.log(data);
                if(0 == data.rc){
                    // console.log('getSysInfor, succ.=>'+data.errCode);
                    // this.store = data.dat;
                    //data = '';
                    this.system = data.dat.system;
                    this.LTEZ = data.dat.LTEZ;
                    this.LTE4G = data.dat.LTE4G;
                    this.GNSS = data.dat.GNSS;
                    this.DHCP = data.dat.DHCP;
                    this.LAN = data.dat.LAN;
                    this.NTP = data.dat.NTP;
                    this.RTMP = data.dat.RTMP;
                    this.VPN = data.dat.VPN;
                    this.FireWall = data.dat.FireWall;
                    this.Mapping = data.dat.Mapping;
                }
                else{
                    yutls.msgBox(data.errCode);
                }
                // console.log('_this.system.=>'+this.system.localDatetime);
            },
            error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
        });
    
    }
    getHostStatusInfo(){
        let str = {
            "command":"getHostStatusInfo"  //"getSysInfor"
        }
        let parameters = JSON.stringify(str);
        $.ajax({
            type:"POST",
            url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
            data:parameters,
            contentType: 'application/json',
            dataType: 'json',
            success: (res)=>{
                // 返回参数格式
    
                //var data = JSON.parse(res);
                var data = res;
                console.log(data);
                if(0 == data.rc){
                    // console.log('getSysInfor, succ.=>'+data.errCode);
                    // this.store = data.dat;
                    //data = '';
                    this.system = data.dat.system;
                    this.LTEZ = data.dat.LTEZ;
                    this.LTE4G = data.dat.LTE4G;
                    this.GNSS = data.dat.GNSS;
                    this.DHCP = data.dat.DHCP;
                    this.LAN = data.dat.LAN;
                    this.NTP = data.dat.NTP;
                }
                else{
                    yutls.msgBox(data.errCode);
                }
                // console.log('_this.system.=>'+this.system.localDatetime);
            },
            error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
        });
    }
    getNetworkCfgInfor(){
        var str = {
            "command":"getNetworkCfgInfor"
        }
        let parameters = JSON.stringify(str);
        $.ajax({
            type:"POST",
            url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
            data:parameters,
            contentType: 'application/json',
            dataType: 'json',
            success: (res)=>{
                //var data = JSON.parse(res);
                var data = res;
                console.log(data);
                if(0 == data.rc){
                    // console.log('getNetworkCfgInfor, succ.=>'+data.errCode);
                    this.RTMP = data.dat.RTMP;
                    this.VPN = data.dat.VPN;
                    this.FireWall = data.dat.FireWall;
                    this.Mapping = data.dat.Mapping;
                }
                else{
                    yutls.msgBox(data.errCode);
                }
                // console.log('_this.system.=>'+this.system.localDatetime);
            },
            error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
        });
    
    }
}

export default CStore;