import htmlTemplate from './CNetvTabGnss.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CNetvTabGnss extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'netvTabGnss',
            aHtml: htmlTemplate, //'./src/views/CSysStatusView/subviews/CNetvTabGnss.html',
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        var enJsMap = this.enJsMap;
        var cnJsMap = this.cnJsMap;
        //注册回调
        // netvTabGnssTargetSimSaveBtn
        $('#netvTabGnssTargetSimSaveBtn').click(()=>{
            var selectedData =  {
                "targetSim": $('#netvTabGnssTargetSimVal').val(),
            }
            var str = {
                "command":"setGnssTargetSim",
                "dat":{
                    "GNSS":selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.GNSS.targetSim = selectedData.targetSim;
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
    }
    // evActived
    myActived(self) {
        let _this = self;
        //GNSS
        $('#netvTabGnssTargetSimVal').val(theApp.m_hMainWnd.oStore.GNSS.targetSim);
    }
}
CNetvTabGnss.prototype.Data = {};
CNetvTabGnss.prototype.enHtmlMap = {
    netvTabGnssTitleTxt:"GNSS Configuration", netvTabGnssTargetSimTxt:"Dest. SIM Number", netvTabGnssTargetSimSaveBtn:"Save",
};
CNetvTabGnss.prototype.cnHtmlMap = {
    netvTabGnssTitleTxt:"GNSS配置", netvTabGnssTargetSimTxt:"目标SIM卡号", netvTabGnssTargetSimSaveBtn:"保存",
};
CNetvTabGnss.prototype.enJsMap = {operationSucc:'Operation is successful.',};
CNetvTabGnss.prototype.cnJsMap = {operationSucc:'操作成功.',};


export default CNetvTabGnss;