
/**
 * CVideoView View的主Class定义
 */
import htmlTemplate from './CVideoView.html';
import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';
import videojs from 'video.js';
import "videojs-flash";
import "video.js/dist/video-js.min.css";

/*** CVideoView View的主Class定义 * */
class CVideoView extends CView{
    constructor(hParent){
        // supper call before user 'this' pointer.
        super();
        var _this = this;
        //1. internal variant / function
        _this.player;
        _this.rtmpUrl = '';
        // 保存原始 : 自适应宽高-浏览器大小改变时重置大小, 退出时回复
        _this.originalOnSizeHandle = window.onresize;
        // render data
        _this.Data = {};
        // 多语言实现 => 移到Class最后避免影响阅读
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
                aParent: hParent,
                aNodeID: 'videoView',
                aHtml: htmlTemplate, //'./src/views/CVideoView.html',
                aRegCtrlCallBack: _this.regCtrlCb,
                aLanguage: { language: 'cn', 
                            htmlMap: {'cn': _this.cnHtmlMap ,'en': _this.enHtmlMap,},
                            jsMap: {'cn': _this.cnJsMap,'en': _this.enJsMap,} },
                aRenderData: _this.Data,
                aChildViews: [],
                aEvCallback: {evActived: _this.evMyActived, evDeactived: _this.evMyDeactived,},
            };
        // init this class-object
        _this.init(option);
        //2. exported variant / function
    }
    //
    regCtrlCb(){
        //
        // 注册回调
        // 注册select ctrl监听
        $('#vvRtmpUrlSelector').change(()=>{
            this.rtmpUrl = $('#vvRtmpUrlSelector').val();
        });
        //
        $('#vvPlayBtn').click(()=>{
            // srcUrl = "rtmp://127.0.0.1:1935/live/fqr";
            console.log("Current RTMP=>"+this.rtmpUrl);
            let nodeVideoContent = document.getElementById("vvVideoContent");
            if(('' != this.rtmpUrl)&&(null != nodeVideoContent)){
                //
                // 初始化video ctrl
                let nodeVideoCtrl =  document.getElementById("videoCtrl");
                //如果有video ctrl则删除它重建
                if(null != nodeVideoCtrl){
                    if(this.player){
                        console.log('video dispose');
                        // this.player.pause();
                        this.player.dispose();
                    }
                    nodeVideoCtrl.remove();//nodeVideoContent.removeChild(nodeVideoCtrl);
                    console.log('videoCtrl.removed!');
                }
                let videoCtrStr = "<video id='videoCtrl' class='video-js' preload='auto' >" + 
                "<p class='vjs-no-js'>To view this video please enable flash and be sure web browser that supports HTML5 video." +
                "<a href='http://videojs.com/html5-video-support/' target='_blank'> </a> </p> </video>";
                $("#vvVideoContent").append(videoCtrStr);
                console.log('videoCtrl.added in html!');
                //
                // init video.js object and play...
                var options = {
                    sources: [{ 
                        type: "rtmp/flv",  
                        src: this.rtmpUrl 
                    }], 
                    autoplay: true,  
                    controls: true,
                    'techOrder': ['flash'],  
                    'flash': { swf: './swf/video-js.swf'}
                };
                this.player = videojs('videoCtrl', options, ()=>{
                    console.log('video ctrl is ready.');
                    // this.player.play(); //20191118 remove, 'cause the option=> autoplay: true,
                    //
                    this.player.one('playing', ()=>{
                        this._adjustShowAreaSize();
                    });
                    this.player.one('error', (error)=>{ console.log('videojs error::%o'+error); });

                });
            }
            else{
                yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'vvRtmpAddress'));  //'请输入正确的流服务器地址！'
            }
            //
        });
        //注册退出本页面button
        $('#vvExitBtn').click(()=>{
            // $('#vvStopBtn').click();
            if(this.player){
                this.player.pause();
                this.player.dispose();
            }
            this.deactiveView();//回复原始的windows onsize 处理函数
        });
    }
    evMyDeactived(self){
        //回复原始的windows onsize 处理函数
        window.onresize = self.originalOnSizeHandle;
    }
    // evActived
    evMyActived(self) {
        let _this = self;
        // resize
        window.onresize = _this._adjustShowAreaSize;
        // 初始化rtmp链接
        $("#vvRtmpUrlSelector").empty();
        if((undefined != theApp.m_hMainWnd.oStore.RTMP.channelList) && (0 != theApp.m_hMainWnd.oStore.RTMP.channelList.length)){
            theApp.m_hMainWnd.oStore.RTMP.channelList.forEach(function(value,index){
                let rtmpUrl = "rtmp://" + theApp.m_hMainWnd.oStore.RTMP.ServerIP +  ":" + theApp.m_hMainWnd.oStore.rtmp.defaultRtmpSrvPortNum + "/" + theApp.m_hMainWnd.oStore.rtmp.defaultRtmpSrvAppName + "/" + value.Code; //":1935/live/" + value.Code;
                let tr = "<option id='vvRtmpUrlSelector" + index +"'" + " value=" + rtmpUrl + ">" + rtmpUrl + "</option>";
                $("#vvRtmpUrlSelector").append(tr);
            });
            //获得RTMP流频道地址
            _this.rtmpUrl = $('#vvRtmpUrlSelector').val();
        }else{
            yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'vvRtmpAddressError'));//alert('推流地址为空，请先设置推流地址！');
            _this.deactiveView();
        }
    }
    //---- internal functions -----
    _adjustShowAreaSize(){
        var aHeight = document.getElementById("videoCtrl").clientHeight;
        var aWidth = document.getElementById("videoCtrl").clientWidth;
        if(aHeight == 0 || aWidth == 0)
            return;
        //
        let vHeight = 0;
        let vWidth = 0;
        let ratioVideo = aWidth / aHeight;
        // if(ratio > 4)
        //     return;
        //
        var playbackviewNode = document.getElementById("videoView");
        // var pbvShowareaNode = document.getElementById("vvShowarea");
        let clientDomHeight = playbackviewNode.clientHeight - 100 - document.getElementById("vvVideoHeader").clientHeight - document.getElementById("vvVideoFooter").clientHeight;
        let clientDomWidth = document.getElementById("vvOperationView").clientWidth; // vvVideoContent
        let ratioFrame = clientDomWidth / clientDomHeight;
        // console.log('fRate:vRate('+ratioFrame+':'+ratioVideo+')<>frame('+clientDomWidth+'x'+clientDomHeight+')');
    
        //1. 设置为video随动框架尺寸，避免video尺寸不随窗口拖动变化
        vHeight = clientDomHeight; vWidth = clientDomWidth; 
        if(ratioFrame > ratioVideo ){ 
            //2. 此为: 窗口横向很扁情况(按照height为基准拉抻调整)
            vHeight = clientDomHeight;
        }else{ 
            //3. 此为: 窗口纵向很扁情况(按照width为基准拉抻调整)
            vWidth = clientDomWidth;
        }
        //4. 重新设置video尺寸
        document.getElementById("videoCtrl").style.height = vHeight +'px';
        document.getElementById("videoCtrl").style.width = vWidth +'px';
        // console.log('frame('+clientDomWidth+'X'+clientDomHeight+')=>(video::'+vWidth+'X'+vHeight+')');
    }
}
// 多语言实现
CVideoView.prototype.enHtmlMap = {
    //vvVCtrlErr:"This browser can support [video], please try IE 9+,Firefox,Opera,Chrome or Safari.",
    vvPlayBtn:"Play", vvExitBtn:"Exit",//vvPauseBtn:"Pause", vvStopBtn:"Stop", 
};
CVideoView.prototype.cnHtmlMap = {
    //vvVCtrlErr:"浏览器不支持播放video,请试用以下浏览器IE 9+、Firefox、Opera、Chrome 和 Safari.",
    vvPlayBtn:"播放", vvExitBtn:"退出", //, vvPauseBtn:"暂停", vvStopBtn:"停止"
};

CVideoView.prototype.enJsMap = {
    vvRtmpAddress:'Please, input the correct RTMP server URL', vvRtmpAddressError:'RTMP URL is NULL! Please set the address in [Network Config=>RTMP].',
    vvRtmpVideoNodeMissed: 'vvVideoContent node is missed in html!',
}
CVideoView.prototype.cnJsMap = {
    vvRtmpAddress:'请输入正确的流服务器地址！', vvRtmpAddressError:'推流地址为空，请先设置推流地址！',
    vvRtmpVideoNodeMissed: 'vvVideoContent节点在html中不存在！',
}

export default CVideoView;
