import htmlTemplate from './CLteView.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CLteView extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'ssvTabLTE',
            aHtml: htmlTemplate, //'./src/views/CSysStatusView/subviews/CLteView.html',
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        //注册回调
    }
    // evActived
    myActived(self) {
        //获取当前active的TabView
        var tabID = 0;
        tabID = $('#ssvTab .tab-header .tab-header-item').siblings('.tab-header-selected').attr('id');
        if(0 == tabID)
            tabID = 'ssvTabHeaderZ';
        // console.log(tabID);
        //
        switch(tabID)
        {
        case 'ssvTabHeaderZ':
            self.Data.ssvTypeVal = theApp.m_hMainWnd.oStore.LTEZ.type;
            self.Data.ssvConnectStatVal = theApp.m_hMainWnd.oStore.LTEZ.connection;
            self.Data.ssvWLanIPVal = theApp.m_hMainWnd.oStore.LTEZ.wlanIP;
            self.Data.ssvUSimVal = theApp.m_hMainWnd.oStore.LTEZ.usim;
            self.Data.ssvDefaultGWVal = theApp.m_hMainWnd.oStore.LTEZ.defaultGwIP;
            self.Data.ssvIMSIVal = theApp.m_hMainWnd.oStore.LTEZ.IMSI;
            self.Data.ssvMDnsVal = theApp.m_hMainWnd.oStore.LTEZ.mDnsIP;
            self.Data.ssvPlmnVal = theApp.m_hMainWnd.oStore.LTEZ.PLMN;
            self.Data.ssvSDnsVal = theApp.m_hMainWnd.oStore.LTEZ.sDnsIP;
            self.Data.ssvSignalVal = theApp.m_hMainWnd.oStore.LTEZ.signal;
            self.Data.ssvMacVal = theApp.m_hMainWnd.oStore.LTEZ.MAC;
            self.Data.ssvFrqVal = theApp.m_hMainWnd.oStore.LTEZ.frq;
            self.Data.ssvRSRQVal = theApp.m_hMainWnd.oStore.LTEZ.RSRQ;
            // self.Data.ssvSNRVal = theApp.m_hMainWnd.oStore.LTEZ.SNR;
            break;
        case 'ssvTabHeader4G':
            self.Data.ssvTypeVal = theApp.m_hMainWnd.oStore.LTE4G.type;
            self.Data.ssvConnectStatVal = theApp.m_hMainWnd.oStore.LTE4G.connection;
            self.Data.ssvWLanIPVal = theApp.m_hMainWnd.oStore.LTE4G.wlanIP;
            self.Data.ssvUSimVal = theApp.m_hMainWnd.oStore.LTE4G.usim;
            self.Data.ssvDefaultGWVal = theApp.m_hMainWnd.oStore.LTE4G.defaultGwIP;
            self.Data.ssvIMSIVal = theApp.m_hMainWnd.oStore.LTE4G.IMSI;
            self.Data.ssvMDnsVal = theApp.m_hMainWnd.oStore.LTE4G.mDnsIP;
            self.Data.ssvPlmnVal = theApp.m_hMainWnd.oStore.LTE4G.PLMN;
            self.Data.ssvSDnsVal = theApp.m_hMainWnd.oStore.LTE4G.sDnsIP;
            self.Data.ssvSignalVal = theApp.m_hMainWnd.oStore.LTE4G.signal;
            self.Data.ssvMacVal = theApp.m_hMainWnd.oStore.LTE4G.MAC;
            self.Data.ssvFrqVal = theApp.m_hMainWnd.oStore.LTE4G.frq;
            self.Data.ssvRSRQVal = theApp.m_hMainWnd.oStore.LTE4G.RSRQ;
            // self.Data.ssvSNRVal = theApp.m_hMainWnd.oStore.LTE4G.SNR;
        break;
        default:
        
        }
    }
}
CLteView.prototype.Data = {
    ssvTypeVal:'n/a',ssvConnectStatVal:'n/a',ssvWLanIPVal:'n/a',ssvUSimVal:'n/a',ssvDefaultGWVal:'n/a',ssvIMSIVal:'n/a',ssvMDnsVal:'n/a',
    ssvPlmnVal:'n/a',ssvSDnsVal:'n/a',ssvSignalVal:'n/a',ssvMacVal:'n/a',ssvFrqVal:'n/a',ssvRSRQVal:'n/a',
}
CLteView.prototype.enHtmlMap = {ssvTitleIntCfgTxt:"Internet Configuration",ssvTitleNetStatTxt:"Networking Status",ssvTypeTxt:"Module Type",ssvConnectStatTxt:"Connection Status",
    ssvWLanIP:"WLAN IP",ssvUSim:"USIM Status",ssvDefaultGW:"Default Gateway",ssvMDns:"Master DNS",ssvSDns:"Slave DNS",ssvSignal:"Signal(dBm)",ssvMac:"MAC/IMEI Address",ssvFrq:"Band",ssvRSRQ:"Signal RSRQ",//ssvSNR:"SNR(dB)",
};
CLteView.prototype.cnHtmlMap = {ssvTitleIntCfgTxt:"Internet 配置状态",ssvTitleNetStatTxt:"网络状态",ssvTypeTxt:"联机型态",ssvConnectStatTxt:"连接状态",
    ssvWLanIP:"广域网络IP地址",ssvUSim:"USIM卡状态",ssvDefaultGW:"默认网关",ssvMDns:"主DNS",ssvSDns:"次DNS",ssvSignal:"信号强度(dBm)",ssvMac:"MAC/IMEI地址",ssvFrq:"频点(Band)",ssvRSRQ:"信号接收质量RSRQ",//ssvSNR:"信噪比(dB)",
};
CLteView.prototype.enJsMap = {};
CLteView.prototype.cnJsMap = {};

export default CLteView;