import htmlTemplate from './CNetvTabRTMP.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CNetvTabRTMP extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'netvTabRTMP',
            aHtml: htmlTemplate, 
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        var _this = this;
        //注册回调
        // netvTabRTMPApplyCfgBtn
        $('#netvTabRTMPApplyCfgBtn').click(()=>{
            var selectedData =  $('#RTMPInforIPValSelector').children('option:selected').val();
            var str = {
                "command":"setRtmpServerIP",
                "dat":{
                    "RTMP": {
                        "ServerIP": selectedData
                    }
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.RTMP.ServerIP = selectedData;
                        //2. refresh the channel list table
                        $("#netvTabRTMPChannelTable tr:gt(1)").remove();
                        // *** HTML
                        // <tr> <td><input type='checkbox' checked='' /></td> <td><input class='netvTabRTMPChannelNameVal'/></td> <td><input class='netvTabRTMPChannelCodeVal'/></td> <td><a id='netvTabRTMPChannelRefVal'></a></td></tr>
                        theApp.m_hMainWnd.oStore.RTMP.channelList.forEach(function(value,index){
                            let rtmpRefUrl = "rtmp://" + theApp.m_hMainWnd.oStore.RTMP.ServerIP +  ":" + theApp.m_hMainWnd.oStore.rtmp.defaultRtmpSrvPortNum + "/" + theApp.m_hMainWnd.oStore.rtmp.defaultRtmpSrvAppName + "/";//":1935/live/";
                            let tr =  "<tr> <td><input type='checkbox' /></td>" +
                                "<td><input class='netvTabRTMPChannelNameVal' value=" + value.Name + "></td>" +
                                "<td><input class='netvTabRTMPChannelCodeVal' value="+ value.Code+"></td>" +
                                "<td><a style='color: red'>" + rtmpRefUrl + value.Code + "</a></td></tr>"
                            $("#netvTabRTMPChannelTable").append(tr);
                        });

                        yutls.msgBox('操作成功! code:' + data.errCode);
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        // netvTabRTMPNewBtn
        $('#netvTabRTMPNewBtn').click(()=>{
            let tr =  "<tr> <td><input type='checkbox' /></td>" +
                "<td><input class='netvTabRTMPChannelNameVal' value=''></td>" +
                "<td><input class='netvTabRTMPChannelCodeVal' value=''></td>" +
                "<td><a></a></td></tr>"
            $("#netvTabRTMPChannelTable").append(tr);

        });
        // netvTabRTMPDelBtn
        $('#netvTabRTMPDelBtn').click(()=>{
            yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'nvDelLinesWarning'));//alert('所有选中的行将被删除！');
            // get the selected idx file information
            $(":checkbox:checked","#netvTabRTMPChannelTable").each(function(){
                let idx = $(this).parents("tr").index();  // 获取checkbox所在行的顺序
                $("#netvTabRTMPChannelTable").find("tr:eq("+ idx +")").remove();
            });

        });
        // netvTabRTMPApplyBtn
        $('#netvTabRTMPApplyBtn').click(()=>{
            var parametersErr = '';
            var selectedData = [];
            $("#netvTabRTMPChannelTable").find("tr").each(function(idx, item){
                var name = $(item).find(".netvTabRTMPChannelNameVal").val();
                var code = $(item).find(".netvTabRTMPChannelCodeVal").val();
                // console.log(idx);
                if((undefined != name) && (undefined != code)){
                    if('' == name || '' == code){
                        //console.log(idx);
                        parametersErr = yutls.jsSwitchLang(_this._oLanguage, 'nvChannelValErr')+ (idx-1) +")"; //"频道名称和频道编码取值无效(无效行号="
                        return false; // 用此方法退出jQuery each loop
                    }

                    let cfg = {
                        "Name": name,
                        "Code": code,
                        };
                    selectedData.push(cfg);
                }
            });
            if('' != parametersErr){
                alert(parametersErr);
                return;
            }
            var str = {
                "command":"setRtmpChannel",
                "dat":{
                    "RTMP": {
                        "channelList": selectedData
                    }
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.RTMP.channelList = selectedData;
                        // update table cells
                        if((undefined != theApp.m_hMainWnd.oStore.RTMP.channelList) && (0 != theApp.m_hMainWnd.oStore.RTMP.channelList.length)){
                            $("#netvTabRTMPChannelTable tr:gt(1)").remove();//empty("");
                            // *** HTML
                            // <tr> <td><input type='checkbox' checked='' /></td> <td><input class='netvTabRTMPChannelNameVal'/></td> <td><input class='netvTabRTMPChannelCodeVal'/></td> <td><a id='netvTabRTMPChannelRefVal'></a></td></tr>
                            theApp.m_hMainWnd.oStore.RTMP.channelList.forEach(function(value,index){
                                let rtmpRefUrl = "rtmp://" + theApp.m_hMainWnd.oStore.RTMP.ServerIP + ":1935/live/";
                                let tr =  "<tr> <td><input type='checkbox' /></td>" +
                                    "<td><input class='netvTabRTMPChannelNameVal' value=" + value.Name + "></td>" +
                                    "<td><input class='netvTabRTMPChannelCodeVal' value="+ value.Code+"></td>" +
                                    "<td><a>" + rtmpRefUrl + value.Code + "</a></td></tr>"
                                $("#netvTabRTMPChannelTable").append(tr);
                            });
                        }
                        //
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                        console.log(data.dat);
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
    }
    // evActived
    myActived(self) {
        let _this = self;
        //1. init the RTMP server table. rtmp://127.0.0.1:1935/live/fqr
        let defaultPortNum = theApp.m_hMainWnd.oStore.rtmp.defaultRtmpSrvPortNum, defaultAppName = theApp.m_hMainWnd.oStore.rtmp.defaultRtmpSrvAppName;
        $("#RTMPInforIPValSelector").empty();
        //<option value=''>10.0.11.1(LAN1)</option>
        let rtmpUrl = "rtmp://";
        theApp.m_hMainWnd.oStore.LAN.accessList.forEach(function(value,index){
            let tr = "<option id='RTMPInforIPValSelector" + index +"'" + " value=" + value.IP + ">" + value.IP + " (" + value.port + ")" + "</option>";
            $("#RTMPInforIPValSelector").append(tr);
            if(theApp.m_hMainWnd.oStore.RTMP.ServerIP == value.IP){
                $("#"+"RTMPInforIPValSelector"+ index).attr("selected",true);
                rtmpUrl = rtmpUrl + theApp.m_hMainWnd.oStore.RTMP.ServerIP + ":" + defaultPortNum + "/" + defaultAppName + "/";//":1935/live/";
            }
        });
        _this.Data.RTMPInforAddrVal = rtmpUrl;
        $('#RTMPInforAppVal').val(defaultAppName);
        //注册text监听 20190723：为简化后台处理当前设置为app input为只读，所以以下text监听无效
        $("#RTMPInforAppVal").bind("input propertychange",function(event){
            let strApp = $('#RTMPInforAppVal').val();
            if(strApp == ""){
                defaultAppName = "live";
            }else{
                defaultAppName = $('#RTMPInforAppVal').val();
            }
            theApp.m_hMainWnd.oStore.rtmp.defaultRtmpSrvAppName = defaultAppName;
            $('#RTMPInforAppVal').val(defaultAppName);
            //
            let strIP = $('#RTMPInforIPValSelector').val();
            rtmpUrl = "rtmp://" + strIP + ":" + defaultPortNum + "/" + defaultAppName + "/";// ":1935/live/";
            _this.Data.RTMPInforAddrVal = rtmpUrl;
            $('#RTMPInforAddrVal').css({"color": "red"});
        });
        //注册select ctrl监听
        $('#RTMPInforIPValSelector').change(()=>{
            _this.Data.RTMPInforAddrVal = ''; //清空
            //
            let strApp = $('#RTMPInforAppVal').val();
            if(strApp == ""){
                defaultAppName = "live";
            }else{
                defaultAppName = $('#RTMPInforAppVal').val();
            }
            theApp.m_hMainWnd.oStore.rtmp.defaultRtmpSrvAppName = defaultAppName;
            $('#RTMPInforAppVal').val(defaultAppName);
            //
            let strIP = $('#RTMPInforIPValSelector').val();
            rtmpUrl = "rtmp://" + strIP + ":" + defaultPortNum + "/" + defaultAppName + "/";// ":1935/live/";
            _this.Data.RTMPInforAddrVal = rtmpUrl;
            $('#RTMPInforAddrVal').css({"color": "red"});
        });
        //2. init the channel list table
        if((undefined != theApp.m_hMainWnd.oStore.RTMP.channelList) && (0 != theApp.m_hMainWnd.oStore.RTMP.channelList.length)){
            $("#netvTabRTMPChannelTable tr:gt(1)").remove();//empty("");
            // *** HTML
            // <tr> <td><input type='checkbox' checked='' /></td> <td><input class='netvTabRTMPChannelNameVal'/></td> <td><input class='netvTabRTMPChannelCodeVal'/></td> <td><a id='netvTabRTMPChannelRefVal'></a></td></tr>
            theApp.m_hMainWnd.oStore.RTMP.channelList.forEach(function(value,index){
                let rtmpRefUrl = "rtmp://" + theApp.m_hMainWnd.oStore.RTMP.ServerIP + ":1935/live/";
                let tr =  "<tr> <td><input type='checkbox' /></td>" +
                    "<td><input class='netvTabRTMPChannelNameVal' value=" + value.Name + "></td>" +
                    "<td><input class='netvTabRTMPChannelCodeVal' value="+ value.Code+"></td>" +
                    "<td><a>" + rtmpRefUrl + value.Code + "</a></td></tr>"
                $("#netvTabRTMPChannelTable").append(tr);
            });
        }
    }

}
CNetvTabRTMP.prototype.Data = {
    RTMPInforAddrVal: 'n/a',
};
CNetvTabRTMP.prototype.enHtmlMap = {
    netvTabRTMPApplyCfgBtn:"Apply Cfg",RTMPInforTitle:"RTMP Configuration",RTMPInforIPTxt:"Server IP",RTMPInforPortTxt:"Port",RTMPInforAppTxt:"APP",RTMPInforAddrTxt:"Push URL",RTMPInforIPValSelectorWarning:"Attention：If selecte one, LAN_IP will be set to 'Static', DHCP will be disbaled!",netvTabRTMPNewBtn:"New", netvTabRTMPDelBtn:"Del", netvTabRTMPApplyBtn:"Apply All",netvTabRTMPChannelTableTitle:"RTMP Channel conf.",netvTabRTMPChannelNameTxt:"Channel Name",netvTabRTMPChannelNoTxt:"Channel Code",netvTabRTMPChannelRefTxt:"Full Address",
};
CNetvTabRTMP.prototype.cnHtmlMap = {
    netvTabRTMPApplyCfgBtn:"应用配置修改",RTMPInforTitle:"RTMP Server设置",RTMPInforIPTxt:"Server IP",RTMPInforPortTxt:"端口号",RTMPInforAppTxt:"推流APP名",RTMPInforAddrTxt:"推流地址",RTMPInforIPValSelectorWarning:"注意：选中后IP地址对应的LAN口会被设置为Static, DHCP将失效！",netvTabRTMPNewBtn:"新建", netvTabRTMPDelBtn:"删除", netvTabRTMPApplyBtn:"应用所有",netvTabRTMPChannelTableTitle:"RTMP频道配置表",netvTabRTMPChannelNameTxt:"频道名称",netvTabRTMPChannelNoTxt:"频道编码",netvTabRTMPChannelRefTxt:"频道引用地址",
};
CNetvTabRTMP.prototype.enJsMap = {
    operationSucc:'Operation is successful.',
    nvDelLinesWarning:'All selected lines will be deleted!',nvChannelValErr:"Channel name or code is incorrect.(line=",
}
CNetvTabRTMP.prototype.cnJsMap = {
    operationSucc:'操作成功.',
    nvDelLinesWarning:'所有选中的行将被删除！', nvChannelValErr:"频道名称和频道编码取值无效(无效行号=",
}


export default CNetvTabRTMP;