import htmlTemplate from './CNetvTabLan.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CNetvTabLan extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'netvTabLan',
            aHtml: htmlTemplate, 
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        var _this = this;
        //注册回调
        // netvTabLanApplyLanCfgBtn
        $('#netvTabLanApplyLanCfgBtn').click(()=>{
            var parametersErr = '';
            var maskVal = ''; // just support ONE VLAN, so the mask should be SAME for all LANs.
            var selectedData = [];
            $("#netvTabLanCfgListTable").find("tr").each(function(idx, item){
                var port = $(item).find("#netvTabLanCfgListTablePort").text();
                if('' != port){
                    var mac = $("#netvTabLanCfgListTable").find("tr:eq("+ idx +")").find("#netvTabLanCfgListTableMac").text();
                    let ip = $("#netvTabLanCfgListTable").find("tr:eq("+ idx +")").find("#netvTabLanCfgListTableIP").val();
                    let mask = $(item).find("select").children('option:selected').val();
                    if(maskVal == ''){ // check if the mask are same.
                        maskVal = mask;
                    }
                    else if(maskVal != mask){
                        parametersErr = yutls.jsSwitchLang(_this._oLanguage, 'nvMaskValErr')+ (idx-1) +")"; //""
                        return false; // 用此方法退出jQuery each loop
                    }
                    if(undefined != ip){
                        if(('' == ip)||(false == yutls.chkIpAddress(ip))){
                            // alert("IP不能为空!");
                            parametersErr = yutls.jsSwitchLang(_this._oLanguage, 'nvIPValErr')+ (idx-1) +")"; //"IP地址取值异常(无效行号="
                            return false; // 用此方法退出jQuery each loop
                        }
                        // let rtmp = '';
                        // if($(item).find("input").is(":checked")){
                        //     rtmp = '1';
                        // }
                        // else{
                        //     rtmp = '0';
                        // }
                        var cfg = {"port": port,"MAC": mac,"IP": ip, "subMask": mask}; //, "RTMP": rtmp
                        selectedData.push(cfg);
                    }
                }
            });
            if('' != parametersErr){
                alert(parametersErr);
                return;
            }
            // "dat": {
            //     "LAN":{ "LAN":[{"port":"lan1/2/3", "IP":"xx", "subMask":"xxx", "RTMP":"0"},{..}]}
            //   }
            var str = {
                "command":"setLanCfg",
                "dat":{
                    "LAN": {
                        "accessList": selectedData
                    }
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.LAN.accessList = selectedData;
                        yutls.msgBox(yutls.jsSwitchLang(_this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        // netvTabLanApplyDhcpBtn
        $('#netvTabLanApplyDhcpBtn').click(()=>{
            // "dat": {
            //     "DHCP":{"dhcpStatus":"DHCP/Statics",
            //           "startIP":"xx", "endIP":"xx", "leaseTerm":"xx"
            //           "subMask":"xx", "defaultGwIP":"xx","DNS1":"xxx", "DNS2":"xxx"}
            //    }
            var startIP = $('#netvTabLanSIPVal').val();
            var endIP = $('#netvTabLanEIPVal').val();
            var defaultGwIP = $('#netvTabLanDefaultGWVal').val();
            var DNS1 = $('#netvTabLanDhcpDNS1Val').val();
            var DNS2 = $('#netvTabLanDhcpDNS2Val').val();
            if((false == yutls.chkIpAddress(startIP))||
                (false == yutls.chkIpAddress(endIP))||
                (false == yutls.chkIpAddress(defaultGwIP))||
                (false == yutls.chkIpAddress(DNS1))||
                (false == yutls.chkIpAddress(DNS2))){
                yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'invalidIPVal')); //发现无效的IP地址
                return;
            }
            var selectedData =  {
                "dhcpStatus": $("#netvTabLan input[name=netvTabLanDHCPRadio]:checked").val(),
                "startIP": startIP,
                "endIP": endIP,
                "leaseTerm": $('#netvTabLanLeaseVal').val(),
                "subMask": $("#netvTabLanDhcpSubmaskSelector").children('option:selected').val(),
                "defaultGwIP": defaultGwIP,
                "DNS1": DNS1,
                "DNS2": DNS2
            }
            var str = {
                "command":"setDhcpCfg",
                "dat":{
                    "DHCP":selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.DHCP = selectedData;
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
    }
    // evActived
    myActived(self) {
        let _this = self;
        // fill LAN
        if((undefined != theApp.m_hMainWnd.oStore.LAN.accessList) && (0 != theApp.m_hMainWnd.oStore.LAN.accessList.length)){
            //1.清除表格内容
            $("#netvTabLanCfgListTable tr:gt(1)").remove();//empty("");
            //2.构建表格内容
            /** 数据格式
                "LAN":{ "LAN":[{"port":"lan1", "MAC":"01-21-09", "IP":"10.1.1.10", "subMask":"255.255.255.0", "RTMP":"1/0"}, {..}],
            ***/
            theApp.m_hMainWnd.oStore.LAN.accessList.forEach(function(value,index){
                let number = index + 1;
                let tr = "<tr class='cfglines'><td id='netvTabLanCfgListTablePort'>"+ value.port+"</td> <td id='netvTabLanCfgListTableMac'>"+ value.MAC+"</td> <td><input id='netvTabLanCfgListTableIP' type='IP' placeholder='10.10.10.11' style='width: 150px;' value="+ value.IP + "></td>" +
                        " <td><select class='netvTabLanCfgListTableSelect' id='netvTabLanCfgListTableSelect" + index +"'" +"> <option value='255.255.255.0'>255.255.255.0</option> <option value='255.255.0.0'>255.255.0.0</option> <option value='255.0.0.0'>255.0.0.0</option> </select></td>" +
                        " <td><a>" + value.type + "</a></td>" +
                        // " <td><input type='radio' name='lan-rtmp' id='netvTabLanCfgListTableRadio" + index +"'" +"/>RTMP Server:1935</td>" +
                        "</tr>";
                $("#netvTabLanCfgListTable").append(tr);
                $("#"+"netvTabLanCfgListTableSelect"+ index).find("option:contains('" + value.subMask + "')").attr("selected",true);
                // if(1 == parseInt(value.RTMP)){
                //     $("#"+"netvTabLanCfgListTableRadio"+ index).prop("checked", true);
                // }
                // else{
                //     $("#"+"netvTabLanCfgListTableRadio"+ index).prop("checked", false);
                // }
            })
            // reg ctrl
            $(".netvTabLanCfgListTableSelect").change(_this.netvTabLanCfgListTableSelectOnChange);
            // trigger the last selector onchange; align to the SAME submask
            // onchange func will be triggered by three times.
            $(".netvTabLanCfgListTableSelect").trigger("change");
        }
        else{
            tools.msgBox(tools.jsSwitchLang(enJsMap, cnJsMap, 'nvPortCfgErr'));//'端口配置异常，请检查设备！'
            return;
        }
        //DHCP
        $('#netvTabLanSIPVal').val(theApp.m_hMainWnd.oStore.DHCP.startIP);
        $('#netvTabLanEIPVal').val(theApp.m_hMainWnd.oStore.DHCP.endIP);
        $('#netvTabLanLeaseVal').val(theApp.m_hMainWnd.oStore.DHCP.leaseTerm);
        $('#netvTabLanDefaultGWVal').val(theApp.m_hMainWnd.oStore.DHCP.defaultGwIP);
        $('#netvTabLanDhcpDNS1Val').val(theApp.m_hMainWnd.oStore.DHCP.DNS1);
        $('#netvTabLanDhcpDNS2Val').val(theApp.m_hMainWnd.oStore.DHCP.DNS2);
        $("#netvTabLanDhcpSubmaskSelector").find("option:contains('" + theApp.m_hMainWnd.oStore.DHCP.subMask + "')").attr("selected",true);
        // $("#netvTabLan input[name=netvTabLanDHCPRadio][value='" + theApp.m_hMainWnd.oStore.DHCP.dhcpStatus + "']").attr('checked','checked');
        // 20190720: 为避免下层送上的参数有大小写失误，选用以下代码
        let dhcpStatus = theApp.m_hMainWnd.oStore.DHCP.dhcpStatus;
        if((dhcpStatus == 'DHCP')||(dhcpStatus == 'Dhcp')||(dhcpStatus == 'dhcp')){
            $('#netvTabLanDHCPRadioStaticsCtrl').removeAttr("checked");
            $('#netvTabLanDHCPRadioDhcpCtrl').attr('checked','checked');
        }else{ // if((dhcpStatus == 'Statics')||(dhcpStatus == 'STATICS')||(dhcpStatus == 'statics')){
            $('#netvTabLanDHCPRadioDhcpCtrl').removeAttr("checked");
            $('#netvTabLanDHCPRadioStaticsCtrl').attr('checked','checked');
        }

    }
    //networks LAN submask设置为一个值, 即只支持一个VLAN.
    netvTabLanCfgListTableSelectOnChange(node){
        let submask = $(this).children('option:selected').val();
        $(".netvTabLanCfgListTableSelect").val(submask);
        // for(let index=0; index < 3; index++){
        //     $("#"+"netvTabLanCfgListTableSelect"+ index).val(submask);
        // }
    }
}
CNetvTabLan.prototype.Data = {};
CNetvTabLan.prototype.enHtmlMap = {
    netvTabLanApplyLanCfgBtn:"Apply StaticAddr",nvtlanCfgTitle:"LAN Configuration", nvtlanDHCPCfgTitle:"DHCP Configuration",netvTabLanSubmask:"Network mask",netvTabLanDevType:"Device Type",/*netvTabLanRTMPServer:"RTMP Server IP",*/netvTabLanSIP:"Start IP address",netvTabLanEIP:"End IP address",netvTabLanLease:"Lease Term(min)",netvTabLanSubmask2:"Network mask",netvTabLanDefaultGW:"Default Gateway",netvTabLanApplyDhcpBtn:"Apply All",
};
CNetvTabLan.prototype.cnHtmlMap = {
    netvTabLanApplyLanCfgBtn:"应用LAN静态地址设置",nvtlanCfgTitle:"LAN 静态地址设置", nvtlanDHCPCfgTitle:"DHCP 配置", netvTabLanSubmask:"子网掩码",netvTabLanDevType:"设备类型",/*netvTabLanRTMPServer:"设置为RTMP服务器IP",*/ netvTabLanSIP:"起始IP地址",netvTabLanEIP:"终止IP地址",netvTabLanLease:"地址租期(分钟)",netvTabLanSubmask2:"子网掩码",netvTabLanDefaultGW:"默认网关",netvTabLanApplyDhcpBtn:"应用DHCP修改",
};
CNetvTabLan.prototype.enJsMap = {
    operationSucc:'Operation is successful.',nvPortCfgErr:'Incorrect port cfg, please check the device.', 
    nvMaskValErr:'All MASK should be same!incorrect(line=',nvIPValErr: "IP value is incorrect(line=",
    invalidIPVal:"!! Invalid ip address is found !!", 
}
CNetvTabLan.prototype.cnJsMap = {
    operationSucc:'操作成功.',nvPortCfgErr:'端口配置异常，请检查设备！', 
    nvMaskValErr:'所有MASK应该相同(无效行号=',nvIPValErr: "IP地址取值异常(无效行号=", 
    invalidIPVal:"!! 无效的IP地址 !!", 
}


export default CNetvTabLan;