import htmlTemplate from './CNetvTabVpn.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CNetvTabVpn extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'netvTabVpn',
            aHtml: htmlTemplate, //'./src/views/CSysStatusView/subviews/CNetvTabVpn.html',
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        var enJsMap = this.enJsMap;
        var cnJsMap = this.cnJsMap;
        //注册回调
        // netvTabVpnConnectBtn
        $('#netvTabVpnConnectBtn').click(()=>{
            // "dat": {
            //     "VPN":{"vpnAddress":"xx", "vpnUser":"xx", "vpnPwd":"xx",
            //         "vpnProtocol":"PPTP/L2TP","vpnKey":"xx"}
            var selectedData =  {
                    "vpnAddress": $('#netvTabVpnAddrVal').val(),
                    "vpnUser": $('#netvTabVpnNameVal').val(),
                    "vpnPwd": $('#netvTabVpnPwdVal').val(),
                    "vpnProtocol": $('#netvTabVpnProtocolSelector').children('option:selected').val(),
                    "vpnKey": $('#netvTabVpnKeyVal').val()
                }
            var str = {
                "command":"connectVPN",
                "dat":{
                    "VPN": selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // "dat": {
                    //     "VPN":{"vpnStatus":"on/off"}
                    //   }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.VPN = selectedData;
                        theApp.m_hMainWnd.oStore.VPN.vpnStatus = data.dat.VPN.vpnStatus;
                        this.Data.netvTabVpnStatusVal = theApp.m_hMainWnd.oStore.VPN.vpnStatus;
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
    }
    // evActived
    myActived(self) {
        let _this = self;
        $('#netvTabVpnAddrVal').val(theApp.m_hMainWnd.oStore.VPN.vpnAddress);
        $('#netvTabVpnNameVal').val(theApp.m_hMainWnd.oStore.VPN.vpnUser);
        $('#netvTabVpnPwdVal').val(theApp.m_hMainWnd.oStore.VPN.vpnPwd);
        $("#netvTabVpnProtocolSelector").find("option:contains('" + theApp.m_hMainWnd.oStore.VPN.vpnProtocol + "')").attr("selected",true);
        $('#netvTabVpnKeyVal').val(theApp.m_hMainWnd.oStore.VPN.vpnKey);
        _this.Data.netvTabVpnStatusVal = theApp.m_hMainWnd.oStore.VPN.vpnStatus;
    }
}
CNetvTabVpn.prototype.Data = {
    netvTabVpnStatusVal: 'n/a',
};
CNetvTabVpn.prototype.enHtmlMap = {
    netvTabVpnTitle:"VPN Passthrough",netvTabVpnAddr:"VPN Address",netvTabVpnName:"VPN User Name",netvTabVpnPwd:"VPN Password",netvTabVpnProtocol:"Protocol",netvTabVpnKey:"Sec. Key",netvTabVpnStatus:"Connection",netvTabVpnConnectBtn:"Connect",
};
CNetvTabVpn.prototype.cnHtmlMap = {
    netvTabVpnTitle:"VPN 穿透",netvTabVpnAddr:"VPN地址",netvTabVpnName:"VPN用户名",netvTabVpnPwd:"VPN密码",netvTabVpnProtocol:"协议",netvTabVpnKey:"连接身份密钥",netvTabVpnStatus:"VPN连接状态",netvTabVpnConnectBtn:"连接",
};
CNetvTabVpn.prototype.enJsMap = {operationSucc:'Operation is successful.',};
CNetvTabVpn.prototype.cnJsMap = {operationSucc:'操作成功.',};


export default CNetvTabVpn;