/**
 * CSysMaintView View的主Class定义
 */
import htmlTemplate from './CSysMaintView.html';
import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CSysMaintView extends CView{
    constructor(hParent) {//-- 构造函数
        // supper call before user 'this' pointer.
        super();
        var _this = this;
        //1.变量+函数
        _this.Data = {
            smvTimeVal: 'n/a',smvFWVerVal:'n/a',//smvHWIpVal:0,smvHWIMEIVal:0, smvNewFWMd5Val:'n/a',smvNewFWVerVal:'n/a',
        }
        //
        let option = {
            aParent: hParent,
            aNodeID: 'sysMaintView',
            aHtml: htmlTemplate, //'./src/views/CSysMaintView.html',
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: { evActived: _this.evMyActived, },
        };
        //2.初始化对象和成员函数
        this.init(option);
    }
    regCtrlCb() {
        var enJsMap = this.enJsMap;
        var cnJsMap = this.cnJsMap;
        //------------ 注册回调-----------
        //  NTP同步时间
        $('#smvRefreshBtn').click(()=>{
            var ip = $('#smvNTPServerIP').val();
            if(false == yutls.chkIpAddress(ip)){
                yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'invalidMacVal')); //发现无效的IP地址 
                return;
            }
            //
            var selectedData = { "serverIP": ip };
            var str = {
                "command":"syncDatetime",
                "dat": {
                    "NTP": selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // "dat":  {"localDatetime":"xxx"} // ms数
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.system.localDatetime = data.dat.localDatetime;
                        theApp.m_hMainWnd.oStore.NTP = selectedData;
                        this.Data.smvTimeVal = yutls.getDateByYMD(theApp.m_hMainWnd.oStore.system.localDatetime);
                        //gAllView.oDashboardView.render();
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'dsvOptSucc') + " code: " + data.errCode);
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        // 刷新设备编码
        $('#smvUpdateHwCodeBtn').click(()=>{
            var mac = $('#smvHwMacVal').val();
            var imei = $('#smvHWIMEIVal').val();
            // check parameters
            if(false == yutls.chkMacAddress(mac)){
                yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'invalidMacVal')); //发现无效的MAC地址 
                return;
            }
            if(false == yutls.chkImeiAddress(imei)){
                yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'invalidImeiVal')); //发现无效的IMEI地址 
                return;
            }
            // POST
            var selectedData = { "hwMAC": mac, "hwIMEI": imei};
            var str = {
                "command":"updateHwCode",
                "dat": {
                    "system": selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.system.hwMAC = selectedData.hwMAC;
                        theApp.m_hMainWnd.oStore.system.hwIMEI = selectedData.hwIMEI;
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'dsvOptSucc') + " code: " + data.errCode);
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        /**
         * 恢复出厂设置
         *  */
        $('#smvSetDefaultBtn').click(()=>{
            if(!confirm(yutls.jsSwitchLang(this._oLanguage,'smvSetDefaultCfg'))){ //'[恢复出厂设置]将擦除所有个人信息, 确认是否继续!'
                // 点击了[取消]
                return;
            }
            //
            let str = 
            {
                "command":"resetToDefault"
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    /* 返回参数格式
                        { "rc": 0/1, "errCode": "xxx"}
                    */
                //var data = JSON.parse(res);
                var data = res;
                console.log(data);
                if(0 == data.rc){
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'dsvOptSucc') + " code: " + data.errCode);
                    }
                    else{
                        yutls.msgBox(res);
                    }
                }
            });
        });
        /**
         * 选取文件
         * 20190716: 文件系统支持原因，暂时屏蔽该功能。!!!!
         *  */
        $('#smvBrowseFileBtn').click(()=>{
            // trigger隐藏的input [file] ctrl,显示openfile对话框
            $("#smvUpgradeFileObj").trigger("click");
            // 获取选取的文件信息
            $("#smvUpgradeFileObj").change(function(){
                $("#smvUpgradeFilePath").val($("#smvUpgradeFileObj").get(0).files[0].name);
            });
        });
        // TODO: mock调试未通过！
        // 20190716: 文件系统支持原因，暂时屏蔽该功能。!!!!
        //上传新固件 TODO：如果不能立即返回，要考虑类似下载链接的轮询处理手段
        //使用 ajax + H5 FormData 类实现
        $('#smvUploadFileBtn').click(()=>{
            // check fileObj is ready
            if(('' == $("#smvUpgradeFilePath").val())){
                yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'smvUpgradeFileNull'));
                return false;
            }
            //
            var fd = new FormData();
            fd.append("command", "uploadFile");
            var fileDat = {
                "fileName": $("#smvUpgradeFileObj").get(0).files[0].name, 
                "fileSize": $("#smvUpgradeFileObj").get(0).files[0].size,
                "fileData": $("#smvUpgradeFileObj").get(0).files[0]
            }
            fd.append("dat", fileDat);
            // let str = 
            // {
            //     "command":"uploadFile",
            //     "dat": { "fileName": "xx", "fileSize":"xx", //size: byte
            //              "fileData": "bin of FormData" }
            // }
            // let parameters = JSON.stringify(str);
            $.ajax({
                url: yutls.getDebugURL(),
                type: "POST",
                processData: false,
                contentType: false,
                data: fd,
                success: (res)=>{
                    // 返回参数格式
                    //     "dat":{"fileName":"xxx",
                    //         "version":"xxx",
                    //         "MD5":"xxx"
                    //         }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc)
                    {
                        if(data.dat.fileName == $("#smvUpgradeFilePath").val())
                        {
                            console.log('uploadFile, succ.=>'+data.dat.version);
                            this.store.user = data.dat.version;
                            this.store.pwd = data.dat.MD5;
                            this.Data.smvNewFWVerVal = data.dat.version;
                            this.Data.smvNewFWMd5Val = data.dat.MD5;
                            yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'smvUpgradeSucc'));//("升级文件已成功上传服务器, 请检查Ver/MD5是否不一致.<br />准备就绪后请点击[重启]完成升级!");
                        }
                        else
                            yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'smvUpgradeFileErr'));
                        }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                }
            });
        });
        /**
         * 重启升级
         * 20190716: 文件系统支持原因，暂时屏蔽该功能。!!!!
         *  */
        $('#smvRebootBtn').click(()=>{
            if(!confirm(yutls.jsSwitchLang(this._oLanguage,'smvRebootConfirm'))){ //'[重启]将重新启动设备, 确认是否继续!'
                // 点击了[取消]
                return;
            }
            //
            let str = 
            {
                "command":"reBoot"
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    /* 返回参数格式
                        { "rc": 0/1, "errCode": "xxx"}
                    */
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'dsvOptSucc') + " code: " + data.errCode);
                    }
                    else{
                        yutls.msgBox(res);
                    }
                }
            });
        });
        /**
         * 刷新log记录
         *  */
        $('#smvRefreshLogBtn').click(()=>{
            // get data from server
            var str = 
            {
                "command": "getLogLink",
                "dat": {
                    "startDatetime": yutls.getTimestamp($('#smvStartDateInput').val()),
                    "endDatetime": yutls.getTimestamp($('#smvEndDateInput').val())
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax(
            { url: yutls.getDebugURL(), // "/cgi-bin/cgi.cgi",
                type: "POST", 
                data: parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                        //var data = JSON.parse(res);
                        var data = res;
                        console.log(data);
                        if(0 == data.rc)
                        {
                            refreshTable(data.dat);
                        }
                        else{
                            yutls.msgBox(res);
                        }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
            
            function refreshTable(data){
                //construct the data table by the res data from server
                //1.清除表格内容
                $("#smvLogFileListTbl  tr:not(:first)").remove();//empty("");
                //2.构建表格内容
                /** 返回数据格式
                    "dat":[
                        {"filePath":"xxx", "fileName":"xxx"},
                        {...}]
                ** html 格式
                    <tr> <td colspan="3"><a id="smvLog">Log记录Link</a></td> </tr>
                ***/
                data.forEach(function(value,index){
                    // var logFileName = value.fileName.replace(/.zip/,'') ;
                    // var DownloadFileName = value.fileName.replace(/.zip/,'.fqr') ;
                    let logFileName = value.fileName;
                    let tr = "<tr>"+
                            " <td colspan=\""+3+"\"><a href=\"" + value.filePath + value.fileName + "\">" + "LinkTo: "+ logFileName + "</a></td> </tr>";
                    $("#smvLogFileListTbl").append(tr);
                })

            }
        });
        // 北斗收发测试
        $('#smvDBbTargetSendBtn').click(()=>{
            var selectedData =  {
                "targetSim": $('#smvDBbTargetNumberVal').val(),
                "msgTxt": $('#smvDBbSendMsgVal').val(),
            }
            var str = {
                "command":"sendDBTestMsg",
                "dat":{
                    "GNSSTest":selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    //{ "rc": 0/1, "errCode": "xxx"  //0 =>成功/1=>失败}
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'dsvOptSucc') + " code: " + data.errCode);
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        $('#smvDBbRecvMsgBtn').click(()=>{
            var selectedData =  {
                "targetSim": $('#smvDBbTargetNumberVal').val(),
            }
            var str = {
                "command":"recvDBTestMsg",
                "dat":{
                    "GNSSTest":selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    //  "rc": 0/1, "errCode": "xxx"  //0 =>成功/1=>失败
                    // "dat": {
                    //     "GNSSTest":{ "targetSim": "xxx", "msgTxt": "xxx"}
                    //   }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        let msg = data.dat.GNSSTest.targetSim + "=>" + data.dat.GNSSTest.msgTxt;
                        $('#smvDBbRecvMsgVal').val(msg);
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
    }
    // evMyActived
    evMyActived(self) {
        self.Data.smvTimeVal = yutls.getDateByYMD(theApp.m_hMainWnd.oStore.system.localDatetime);
        $('#smvHwMacVal').val(theApp.m_hMainWnd.oStore.system.hwMAC);
        $('#smvHWIMEIVal').val(theApp.m_hMainWnd.oStore.system.hwIMEI);
        self.Data.smvFWVerVal = theApp.m_hMainWnd.oStore.system.swVersion;
        if('' == theApp.m_hMainWnd.oStore.NTP.serverIP){
            $('#smvNTPServerIP').val('210.72.145.44');
        }
        else{
            $('#smvNTPServerIP').val(theApp.m_hMainWnd.oStore.NTP.serverIP);
        }
        $('#smvDBbTargetNumberVal').val(theApp.m_hMainWnd.oStore.GNSS.localSim);//fill the 'localSim' as the default val.
    }  
}

CSysMaintView.prototype.enHtmlMap = {smvNTPTitle:"NTP Syn.",smvTime:"Correct time",smvRefreshBtn:"Rrefresh",
    smvTitleGJWH:'Firmware',smvHwMac:'Mac Address',smvHWIMEI:"Hardware Serial",smvUpdateHwCodeBtn:"Update HW Code",smvFWVer:"Firmware Ver.",//smvUpFW:"Upgrade FW",smvNewFW:"New FW",
    smvSetDefaultBtn:"Set default",//smvBrowseFileBtn:"Browse",smvUploadFileBtn:"Upload FW",smvRebootBtn:"Reset",
    smvTitleLog:"Log",smvLogTH:"Log Link",smvLog:"Log List",smvLogStartDate:"Start Datetime",smvLogEndDate:"End Datetime",smvLogOperation:"Operation",smvRefreshLogBtn:"Rrefresh",
    smvTitleBDDebug:"BeiDou Msg Test",smvDBbTargetNumber:"Target SIM", smvDBbTargetSendBtn: "Send", smvDBbRecvMsg: "Recv Msg", smvDBbRecvMsgBtn: "Recv",smvDBbSendMsgTxt:"Test Text",
};
CSysMaintView.prototype.cnHtmlMap = {smvNTPTitle:"NTP网络对时",smvTime:"网络对时",smvRefreshBtn:"刷新",
    smvTitleGJWH:'固件维护',smvHwMac:'本机硬件地址',smvHWIMEI:"本机设备号",smvUpdateHwCodeBtn:"刷新设备编码",smvFWVer:"固件版本号",//smvUpFW:"升级固件",smvNewFW:"新固件",
    smvSetDefaultBtn:"恢复出厂设置",//smvBrowseFileBtn:"选取文件",smvUploadFileBtn:"上传新固件",smvRebootBtn:"重启升级",
    smvTitleLog:"日志维护",smvLogTH:"Log记录链接",smvLog:"Log记录",smvLogStartDate:"开始时间",smvLogEndDate:"结束时间",smvLogOperation:"操作",smvRefreshLogBtn:"刷新日志",
    smvTitleBDDebug:"北斗收发测试",smvDBbTargetNumber:"目标SIM卡号", smvDBbTargetSendBtn: "发送信息", smvDBbRecvMsg: "返回信息", smvDBbRecvMsgBtn: "收取信息",smvDBbSendMsgTxt:"测试文本",
};
CSysMaintView.prototype.enJsMap = {
    dsvOptSucc:"Command is successful！", smvUpgradeFileErr:"The upgrade file is incrroect!",  smvUpgradeFileNull:'Please, pick up a upgrade file!', smvUpgradeSucc:'Success to upgrade the FW file, please check the Ver/MD5 before reboot the device!',
    smvRebootConfirm:'[reboot] will reboot the device, please confirm this operation!', smvSetDefaultCfg:'[SetDefault] will delete all personal date from the device, please confirm this operation!',
    invalidIPVal:"!! Invalid ip address is found !!", invalidMacVal:"'!! Invalid MAC address is found !!",invalidImeiVal:"!! Invalid IMEI address is found !!",
};
CSysMaintView.prototype.cnJsMap = {
    dsvOptSucc:"操作成功！", smvUpgradeFileErr:"服务器收到的升级文件名不一致，请检查！", smvUpgradeFileNull:'上载文件未正确选取!', smvUpgradeSucc:"升级文件已成功上传服务器, 请检查Ver/MD5是否不一致. \n准备就绪后请点击[重启]完成升级!",
    smvRebootConfirm:'[重启]将导致当前业务中断, 确认是否继续!', smvSetDefaultCfg:'[恢复出厂设置]将擦除所有个人信息, 确认是否继续!',
    invalidIPVal:"!! 无效的IP地址 !!", invalidMacVal:"!! 无效的MAC地址  !!",invalidImeiVal:"!! 无效的IMEI地址  !!",
};

export default CSysMaintView;