import htmlTemplate from './CLanView.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CLanView extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'ssvTabLan',
            aHtml: htmlTemplate, //'./src/views/CSysStatusView/subviews/CLanView.html',
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        //注册回调
    }
    // evActived
    myActived(self) {
        //LAN
        self.Data.ssvTabDHCPStatusVal = theApp.m_hMainWnd.oStore.DHCP.dhcpStatus;
        //1.清除表格内容
        $("#ssvTabLanAccessListTbl tr:gt(1)").empty("");
        //2.构建表格内容
        /** 返回数据格式
            "LAN":{ "accessList":[{"MAC":"xx", "IP":"xx", "type":"xx"},{..}]}
        ** html 格式
            <tr> <td><a>1</a></td><td><a>LAN1</a></td><td><a>01-02-01</a></td> <td><a>129.1.1.2</a></td> <td><a>mobile</a></td></tr>
        ***/
        theApp.m_hMainWnd.oStore.LAN.accessList.forEach(function(value,index){
            let number = index + 1;
            let tr = "<tr>"+
                    " <td><a>" + number + "</a></td> <td><a>"+value.port+"</a></td> <td><a>"+ value.MAC + "</a></td> <td><a>" + value.IP + "</a></td> <td><a>" + value.type + "</a></td></tr>";
            $("#ssvTabLanAccessListTbl").append(tr);
        })
    }
}
CLanView.prototype.Data = {
    ssvTabDHCPStatusVal:'n/a',
}
CLanView.prototype.enHtmlMap = {
    ssvtlanInforTitle:"LAN Information",ssvTabDHCPCfg:"DHCP cfg",ssvTabLanDevLst:"Accessed List",ssvTabLanDevMac:'Device MAC',ssvTabLanDevIP:'Device IP',ssvTabLanDevType:"Device Type", 
};
CLanView.prototype.cnHtmlMap = {
    ssvtlanInforTitle:"LAN 信息",ssvTabDHCPCfg:"DHCP配置",ssvTabLanDevLst:"设备接入列表",ssvTabLanDevMac:'设备MAC',ssvTabLanDevIP:'设备IP',ssvTabLanDevType:"设备类型", 
};
CLanView.prototype.enJsMap = {};
CLanView.prototype.cnJsMap = {};

export default CLanView;