import htmlTemplate from './CUserMgtView.html';
import $ from 'jquery';
import {yutls, CView} from 'yut';
/**
 * CUserMgtView View的主Class定义
 * */
class CUserMgtView extends CView{
    constructor(hParent){
        // supper call before user 'this' pointer.
        super();
        var _this = this;
        //1. internal variant / function
        _this.store = {user: 'admin', pwd: ''};
        _this.Data = {};
        // 多语言实现 => 移到Class最后避免影响阅读
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
                aParent: hParent,
                aNodeID: 'userMgtView',
                aHtml: htmlTemplate, //'./src/views/CUserMgtView.html',
                aRegCtrlCallBack: this.regCtrlCb,
                aLanguage: { language: 'cn', 
                            htmlMap: {'cn': _this.cnHtmlMap ,'en': _this.enHtmlMap,},
                            jsMap: {'cn': _this.cnJsMap,'en': _this.enJsMap,} },
                aRenderData: _this.Data,
                aChildViews: [],
                aEvCallback: {evActived: _this.evMyActived,},
            };
        // init this class-object
        _this.init(option);
        //2. exported variant / function
    }
    //
    regCtrlCb(){
        //*********** 注册回调
        // 重置为缺省值
        $('#uvResetBtn').click(()=>{
            var str = {
                "command":"resetDefaultPwd"
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    //    { "rc": 0/1, "errCode": "xxx"}  //0 =>成功/1=>失败
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        this.store.pwd = data.dat.pwd;
                        $('#uvNewPWDInput').val('');
                        $('#uvConfirmNewPWDInput').val('');
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'umvResetPwdSucc')); //'重置密码成功!'
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
    
        });
        $('#uvSaveBtn').click(()=>{
            let oldPwd = $('#uvOldPWDInput').val();
            let newPwd = $('#uvNewPWDInput').val();
            let confirmPwd = $('#uvConfirmNewPWDInput').val();
            //if((oldPwd != this.store.pwd) || (newPwd != confirmPwd) || ('' == newPwd) || ('' == confirmPwd)){
            if((newPwd != confirmPwd) || ('' == newPwd) || ('' == confirmPwd)){
                yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'umvPwdCheckFail'));//'密码校验未通过！'
                return;
            }
            // setPWD
            var str = {
                "command":"setPWD",
                "dat": {
                    "old": oldPwd,
                    "new": newPwd
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: 'application/json',
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    //    { "rc": 0/1, "errCode": "xxx"}  //0 =>成功/1=>失败
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'umvSetPwdSucc'));//'密码设置成功！'
                    }
                    else{
                        //yutls.msgBox(.errCode);
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'umvPwdCheckFail'));//'密码校验未通过！'
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
    }
    // evMyActived
    evMyActived(self) {
        var str = {
            "command":"getUserInfor"
        }
        let parameters = JSON.stringify(str);
        $.ajax({
            type:"POST",
            url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
            data:parameters,
            contentType: 'application/json',
            dataType: 'json',
            success: (res)=>{
                // 返回参数格式
                //     { "rc":0/1, "errCode": error msg tx,
                //     "dat":  {"pwd":"xxx"}
                //    }
                //var data = JSON.parse(res);
                var data = res;
                console.log(data);
                if(0 == data.rc){
                    // console.log('getUserInfor, succ.=>'+data.dat.pwd);
                    self.store.user = 'admin';
                    self.store.pwd = data.dat.pwd;
                }
                else{
                    yutls.msgBox(data.errCode);
                }
            },
            error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
        });
    }
}
// 多语言实现
CUserMgtView.prototype.enHtmlMap = {umvTitle:"User Management",umvName:"User",umvPWD:"Current Password",umvNewPWD:"New Password",umvCmPWD:"Confirm Password", uvResetBtn:"Reset Default",uvSaveBtn:"Save",
}
CUserMgtView.prototype.cnHtmlMap = {umvTitle:"用户管理",umvName:"用户名",umvPWD:"当前密码",umvNewPWD:"新密码",umvCmPWD:"确认新密码", uvResetBtn:"重置为缺省值",uvSaveBtn:"保存",
}
CUserMgtView.prototype.enJsMap = {
    umvResetPwdSucc:'Success to reset password!',umvPwdCheckFail:'Failed to verify the password!',umvSetPwdSucc:'Success to set password!',
}
CUserMgtView.prototype.cnJsMap = {
    umvResetPwdSucc:'重置密码成功!',umvPwdCheckFail:'密码校验未通过！', umvSetPwdSucc:'密码设置成功！',
}

export default CUserMgtView;