import htmlTemplate from './CNetvTabFWFilter.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CNetvTabFWFilter extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'netvTabFWFilter',
            aHtml: htmlTemplate, 
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        var enJsMap = this.enJsMap;
        var cnJsMap = this.cnJsMap;
        //注册回调
        // Firewall and Filter
        // nvTabFWCfgApplyBtn
        $('#nvTabFWCfgApplyBtn').click(()=>{
            var selectedData = {
                "ipFilter": '', "macFilter": '', "DMZ":{"IP":'', "Status":''}
            };
            // $("#netvTabFWFilterTable").find("tr").each(function(idx, item){
                $("#nvTabFWCfgSwIPFilterCkb").is(':checked') ? selectedData.ipFilter = 'on' : selectedData.ipFilter = 'off';
                $("#swMacFilterCkb").is(':checked')? selectedData.macFilter = 'on' : selectedData.macFilter = 'off';
                $("#nvTabFWCfgDMZEnableCkb").is(':checked')? selectedData.DMZ.Status = 'on' : selectedData.DMZ.Status = 'off';
            // });
            var dmzIP = $('#nvTabFWCfgDMZIPAddressVal').val();
            if(false == yutls.chkIpAddress(dmzIP)){
                yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'invalidIPVal')); //发现无效的IP地址
                return;
            }
            selectedData.DMZ.IP = dmzIP;
            // "dat": {
            //     "FireWall":{
            //          "ipFilter":"off", "macFilter":"on", "DMZ":{"IP":"123.3.3.1", "Status":"on"}}
            //   }
            var str = {
                "command":"setFirewall",
                "dat":{
                    "FireWall": selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.FireWall.ipFilter = selectedData.ipFilter;
                        theApp.m_hMainWnd.oStore.FireWall.macFilter = selectedData.macFilter;
                        theApp.m_hMainWnd.oStore.FireWall.DMZ = selectedData.DMZ;
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        // IP filter
        // nvTabFWIPFilterNewBtn
        $('#nvTabFWIPFilterNewBtn').click(()=>{
            // let lines = $("#netvTabFWIPFilterTable").find("tr").length;
            // let index = lines + 10;
            let tr = "<tr> <td><input type='checkbox' /></td> " +
                "<td><input class='nvTabFWIPFilterTblLanIPVal' style='width:100px' value=''></td> <td><input  style='width:100px' class='nvTabFWIPFilterTblLanPortVal' value=''></td>" +
                "<td><input class='nvTabFWIPFilterTblWLanIPVal' style='width:100px' value=''></td> <td><input style='width:100px' class='nvTabFWIPFilterTblWLanPortVal' value=''></td>" +
                "<td><select style='width:100px'><option value='enable'>Enable</option><option selected value='disbale'>Disbale</option></select></td></tr>";
            $("#netvTabFWIPFilterTable").append(tr);
        });
        // nvTabFWIPFilterDelBtn
        $('#nvTabFWIPFilterDelBtn').click(()=>{
            yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'nvDelLinesWarning')); //alert('所有选中的行将被删除！'); //TODO:是否发给服务器再删除？
            // get the selected idx file information
            $(":checkbox:checked","#netvTabFWIPFilterTable").each(function(){
                // alert('所有选中的行都会被删除！');
                let idx = $(this).parents("tr").index();  // 获取checkbox所在行的顺序
                $("#netvTabFWIPFilterTable").find("tr:eq("+ idx +")").remove();
            });
        });
        // nvTabFWIPFilterApplyallBtn
        // 20190521: remove 'Validation' parameter.
        $('#nvTabFWIPFilterApplyallBtn').click(()=>{
            var parametersErr = '';
            var selectedData = [];
            $("#netvTabFWIPFilterTable").find("tr").each((idx, item)=>{
                //var Validation = $(item).find(".nvtpfValiVal").val();
                var LanIPs = $(item).find(".nvTabFWIPFilterTblLanIPVal").val();
                var LanPort = $(item).find(".nvTabFWIPFilterTblLanPortVal").val();
                var WLanIPs = $(item).find(".nvTabFWIPFilterTblWLanIPVal").val();
                var WLanPort= $(item).find(".nvTabFWIPFilterTblWLanPortVal").val();
                //var Protocol= $(item).find(".nvtpfProtocolVal").val();
                var Status = $(item).find("select").children('option:selected').val();
                if(/*(undefined != Validation) && */(undefined != LanIPs) && (undefined != LanPort) && (undefined != WLanIPs) && (undefined != WLanPort) && /*(undefined != Protocol) && */ (undefined != Status)){
                    if(('' == LanIPs) || (false == yutls.chkIpAddress(LanIPs)) || ('' == LanPort) || ('' == WLanIPs) || (false == yutls.chkIpAddress(WLanIPs)) || ('' == WLanPort) || /*('' == Protocol) ||*/ ('' == Status)){
                        // alert("WLAN Port不能为空!");
                        parametersErr = yutls.jsSwitchLang(this._oLanguage, 'nvIcorrectValErr')+ (idx-1) +")";//"取值无效(无效行号="
                        return false; // 用此方法退出jQuery each loop
                    }
                    // let mask = $(item).find("select").children('option:selected').val();
                    let cfg = {//"Validation": Validation,
                            "LanIPs": LanIPs,
                            "LanPort": LanPort,
                            "WLanIPs": WLanIPs,
                            "WLanPort":WLanPort,
                            //"Protocol":Protocol,
                            "Status": Status
                        };
                    selectedData.push(cfg);
                }
            });
            if('' != parametersErr){
                alert(parametersErr);
                return;
            }
            //
            var str = {
                "command":"setIPFilterTable",
                "dat":{
                    "FireWall": {
                        "ipList": selectedData
                    }
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.FireWall.ipList = selectedData;
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        // Mac Filter
        // nvTabFWMacFilterTblNewBtn
        $('#nvTabFWMacFilterTblNewBtn').click(()=>{
            let tr = "<tr> <td><input type='checkbox' /></td> <td><input class='nvTabFWMacFilterTblMacVal'  value=''></td>" +
                "<td><select><option value='enable'>Enable</option> <option selected value='disable'>Disable</option> </select></td>" +
                "<td><input class='nvTabFWMacFilterTblMemoVal' style='width:300px' value=''></td></tr>";
            $("#nvTabFWMacFilterTbl").append(tr);

        });
        // nvTabFWMacFilterTblDelBtn
        $('#nvTabFWMacFilterTblDelBtn').click(()=>{
            yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'nvDelLinesWarning'));//alert('所有选中的行将被删除！'); //TODO:是否发给服务器再删除？
            // get the selected idx file information
            $(":checkbox:checked","#nvTabFWMacFilterTbl").each(function(){
                // alert('所有选中的行都会被删除！');
                let idx = $(this).parents("tr").index();  // 获取checkbox所在行的顺序
                $("#nvTabFWMacFilterTbl").find("tr:eq("+ idx +")").remove();
            });
        });
        // nvTabFWMacFilterTblApplyallBtn
        $('#nvTabFWMacFilterTblApplyallBtn').click(()=>{
            var parametersErr = '';
            var selectedData = [];
            $("#nvTabFWMacFilterTbl").find("tr").each((idx, item)=>{
                var mac = $(item).find(".nvTabFWMacFilterTblMacVal").val();
                var Desc = $(item).find(".nvTabFWMacFilterTblMemoVal").val();
                var Status = $(item).find("select").children('option:selected').val();
                if((undefined != mac)&&(undefined != Desc)&&(undefined != Status)){
                    if(('' == mac)||(false == yutls.chkMacAddress(mac))||('' == Status)/*||('' == Desc)*/){
                        // alert("WLAN Port不能为空!");
                        parametersErr = yutls.jsSwitchLang(this._oLanguage, 'nvIcorrectValErr')+ (idx-1) +")";//"取值无效(无效行号="
                        return false; // 用此方法退出jQuery each loop
                    }
                    let cfg = {"MAC": mac,
                            "Desc": Desc,
                            "Status": Status
                        };
                    selectedData.push(cfg);
                }
            });
            if('' != parametersErr){
                alert(parametersErr);
                return;
            }
            //
            var str = {
                "command":"setMacFilterTable",
                "dat":{
                    "FireWall": {
                        "macList": selectedData
                    }
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.FireWall.macList = selectedData;
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
    }
    // evActived
    myActived(self) {
        let _this = self;
        // $('#ssvTabGnssSatNumVal').text(theApp.m_hMainWnd.oStore.GNSS.satelliteNum);
        var ipFilter, macFilter, DMZ;
        ('on' == theApp.m_hMainWnd.oStore.FireWall.ipFilter)? ipFilter = true : ipFilter = false;
        ('on' == theApp.m_hMainWnd.oStore.FireWall.macFilter)? macFilter = true : macFilter = false;
        ('on' == theApp.m_hMainWnd.oStore.FireWall.DMZ.Status)? DMZ = true : DMZ = false;
        //
        $("#netvTabFWFilterTable").find("tr").each(function(idx, item){
            $(item).find("#nvTabFWCfgSwIPFilterCkb").attr("checked",ipFilter);
            $(item).find("#swMacFilterCkb").attr("checked",macFilter);
            $(item).find("#nvTabFWCfgDMZEnableCkb").attr("checked",DMZ);
            $(item).find("#nvTabFWCfgDMZIPAddressVal").val(theApp.m_hMainWnd.oStore.FireWall.DMZ.IP);
        });
        // IP 过滤表
        // 20190521: remove Validation parameter
        if((undefined != theApp.m_hMainWnd.oStore.FireWall.ipList) && (0 != theApp.m_hMainWnd.oStore.FireWall.ipList.length)){
            $("#netvTabFWIPFilterTable tr:gt(1)").remove();//empty("");
            //"ipList":[{"LanIPs":"127.1.1.2", "LanPort":"3122","WLanIPs":"10.1.1.2", "WLanPort":"213", "Status":"enable"},
            // {"LanIPs":"127.1.1.3", "LanPort":"3123","WLanIPs":"10.1.1.3", "WLanPort":"214", "Status":"diabale"}],
            // ** html 格式
            // <tr> <td><input type='checkbox' checked='' /></td> <td><input class='nvTabFWIPFilterTblLanIPVal'/></td> <td><input class='nvTabFWIPFilterTblLanPortVal'/></td>
            //  <td><input class='nvTabFWIPFilterTblWLanIPVal'/></td> <td><input class='nvTabFWIPFilterTblWLanPortVal'/></td>
            //   <td><select id='nvTabFWIPFilterTblStatusSelector'><option value='enable'>enable</option><option value='disbale'>disbale</option></select></td></tr>
            theApp.m_hMainWnd.oStore.FireWall.ipList.forEach(function(value,index){
                let tr = "<tr> <td><input type='checkbox' /></td>" + // <td><input class='nvtpfValiVal'  style='width:35px' value=" + value.Validation + "></td>
                        "<td><input class='nvTabFWIPFilterTblLanIPVal' style='width:100px' value=" + value.LanIPs +"></td> <td><input  style='width:100px' class='nvTabFWIPFilterTblLanPortVal' value=" + value.LanPort +"></td>" +
                        "<td><input class='nvTabFWIPFilterTblWLanIPVal' style='width:100px' value="+ value.WLanIPs+"></td> <td><input style='width:100px' class='nvTabFWIPFilterTblWLanPortVal' value="+value.WLanPort +"></td>" +
                        "<td><select style='width:100px' id='nvTabFWIPFilterTblStatusSelector"+index+ "'"+"><option value='enable'>Enable</option><option value='disbale'>Disbale</option></select></td></tr>";
                $("#netvTabFWIPFilterTable").append(tr);
                $("#"+"nvTabFWIPFilterTblStatusSelector"+ index).find("option:contains('" + value.Status + "')").attr("selected",true);
            })
        }
        // MAC 过滤表
        if((undefined != theApp.m_hMainWnd.oStore.FireWall.macList) && (0 != theApp.m_hMainWnd.oStore.FireWall.macList.length)){
            $("#nvTabFWMacFilterTbl tr:gt(1)").remove();//empty("");
            //"macList":[{"MAC":"01-29-03", "Status":"enable","Desc":"xx-Max40字符"},
            //           {"MAC":"01-29-04", "Status":"disable","Desc":"xx-Max40字符"}]
            // ** html 格式
            // <tr> <td><input type="checkbox" checked='' /></td> <td><input class='nvTabFWMacFilterTblMacVal'  value=''/>MAC</td>
            // <td><select id="nvTabFWMacFilterTblStatusSelector" > <option value="enable">Enable</option> <option value="disable">Disable</option> </select></td>
            // <td><input class='nvTabFWMacFilterTblMemoVal' value=''/>描述</td> </tr>
                theApp.m_hMainWnd.oStore.FireWall.macList.forEach(function(value,index){
                let tr = "<tr> <td><input type='checkbox' /></td> <td><input class='nvTabFWMacFilterTblMacVal'  value=" + value.MAC + "></td>" +
                        "<td><select id='nvTabFWMacFilterTblStatusSelector"+index+ "'"+"><option value='enable'>Enable</option> <option value='disable'>Disable</option> </select></td>" +
                        "<td><input class='nvTabFWMacFilterTblMemoVal' style='width:300px' value="+ value.Desc+"></td></tr>";
                $("#nvTabFWMacFilterTbl").append(tr);
                $("#"+"nvTabFWMacFilterTblStatusSelector"+ index).find("option:contains('" + value.Status + "')").attr("selected",true);
            })
        }
    }
}
CNetvTabFWFilter.prototype.Data = {};
CNetvTabFWFilter.prototype.enHtmlMap = {
    netvTabFWCfgTitleTxt:"Firewall",netvTabFWIPFilterTblTitleTxt:"IP Filter table",nvTabFWMacFilterTblTitleTxt:"MAC Filter table", nvTabFWCfgIPFilterTXt:"IP Filter",nvTabFWCfgMacFilterTXt:"MAC Filter",
    nvTabFWCfgSwIPFilter:" Turn on IP filter",  swMacFilter:" MAC filter",nvTabFWCfgMDMZTxt:"DMZ host IP",nvTabFWCfgDMZEnableVal:" Enable",nvTabFWCfgApplyBtn:"Apply",
    /*nvtpfValidation:"Validation",*/ nvTabFWIPFilterTblLanIP:"LAN IP",nvTabFWIPFilterTblLanPort:"LAN Port",nvTabFWIPFilterTblWLanIP:"WLAN IP",nvTabFWIPFilterTblWLanPort:"WLAN Port",nvTabFWIPFilterTblStatus:"Status",
    nvTabFWIPFilterNewBtn:"New", nvTabFWIPFilterDelBtn:"Delete", nvTabFWIPFilterApplyallBtn:"Apply All",
    nvTabFWMacFilterTblNewBtn:"New", nvTabFWMacFilterTblDelBtn:"Delete", nvTabFWMacFilterTblApplyallBtn:"Apply All",
    nvTabFWMacFilterTblStatus:"Status",nvTabFWMacFilterTblMemo:"Memo(Max40 char.)",
};
CNetvTabFWFilter.prototype.cnHtmlMap = {
    netvTabFWCfgTitleTxt:"防火墙设置", netvTabFWIPFilterTblTitleTxt:"IP过滤表",nvTabFWMacFilterTblTitleTxt:"MAC过滤表", nvTabFWCfgIPFilterTXt:"IP 过滤",nvTabFWCfgMacFilterTXt:"MAC 过滤",
    nvTabFWCfgSwIPFilter:" 开启IP地址过滤", swMacFilter:" MAC过滤",nvTabFWCfgMDMZTxt:"DMZ主机IP", nvTabFWCfgDMZEnableVal:" 激活",nvTabFWCfgApplyBtn:"应用",
    /*nvtpfValidation:"有效期",*/ nvTabFWIPFilterTblLanIP:"局域网IP地址段",nvTabFWIPFilterTblLanPort:"局域网端口",nvTabFWIPFilterTblWLanIP:"广域网IP地址段",nvTabFWIPFilterTblWLanPort:"广域网端口",nvTabFWIPFilterTblStatus:"状态",
    nvTabFWIPFilterNewBtn:"新建", nvTabFWIPFilterDelBtn:"删除", nvTabFWIPFilterApplyallBtn:"应用所有",
    nvTabFWMacFilterTblNewBtn:"新建", nvTabFWMacFilterTblDelBtn:"删除", nvTabFWMacFilterTblApplyallBtn:"应用所有",
    nvTabFWMacFilterTblStatus:"状态",nvTabFWMacFilterTblMemo:"描述(Max40字符)",
};
CNetvTabFWFilter.prototype.enJsMap = {
    operationSucc:'Operation is successful.',
    nvDelLinesWarning:'All selected lines will be deleted!',nvIcorrectValErr:"Incorrect value(Line=",
    invalidIPVal:"!! Invalid ip address is found !!", invalidMacVal:"'!! Invalid MAC address is found !!",
};
CNetvTabFWFilter.prototype.cnJsMap = {
    operationSucc:'操作成功.',
    nvDelLinesWarning:'所有选中的行将被删除！',nvIcorrectValErr:"取值无效(无效行号=",
    invalidIPVal:"!! 无效的IP地址 !!", invalidMacVal:"!! 无效的MAC地址  !!",
};


export default CNetvTabFWFilter;