import htmlTemplate from './CNetvTabMapping.html';

import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';

class CNetvTabMapping extends CView{
    constructor(hParent) {
        // constructor
        super();
        var _this = this;
        //1. internal variant / function
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
            aParent: hParent,
            aNodeID: 'netvTabMapping',
            aHtml: htmlTemplate, 
            aRegCtrlCallBack: _this.regCtrlCb,
            aLanguage: {
            language: 'cn',
                htmlMap: { 'cn': _this.cnHtmlMap, 'en': _this.enHtmlMap, },
                jsMap: { 'cn': _this.cnJsMap, 'en': _this.enJsMap, }
            },
            aRenderData: _this.Data,
            aChildViews: [],
            aEvCallback: {evActived: _this.myActived}, //evMounted: _this.myMounted, 
        };
        // init this class-object
        this.init(option);
        //2. exported variant / function
    }
    regCtrlCb() {
        var enJsMap = this.enJsMap;
        var cnJsMap = this.cnJsMap;
        //  -------------
        //注册通道映射SlotMapping的Checkbox回调函数
        //即时检查是否符合映射规则
        // LTE-Z 配置
        $('#nvtMChannelZLan1Ckb').change(()=>{
            let rc = this.checkSlotMappingRule('LAN1', 'LTE-Z');
            if(true == rc){
                $('#nvtMChannelZLan1Ckb').prop('checked', true);
                $('#nvtMChannelZAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvPortMapLAN12ZErr') ); //[端口映射]表中已经建立LAN1<->LTE-Z高优先级映射!'
                $('#nvtMChannelZAlertTxt').css({'color':'red'});
            }
            else{
                // $('#nvtMChannelZLan1Ckb').prop('checked', false);
                $('#nvtMChannelZAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvSetPortMap') );//('[设置映射]');
                $('#nvtMChannelZAlertTxt').css({'color':'black'});
            }
        });
        $('#nvtMChannelZLan2Ckb').change(()=>{
            let rc = this.checkSlotMappingRule('LAN2', 'LTE-Z');
            if(true == rc){
                $('#nvtMChannelZLan2Ckb').prop('checked', true);
                $('#nvtMChannelZAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvPortMapLAN22ZErr') ); //'[端口映射]表中已经建立LAN2<->LTE-Z高优先级映射!'
                $('#nvtMChannelZAlertTxt').css({'color':'red'});
            }
            else{
                // $('#nvtMChannelZLan2Ckb').prop('checked', false);
                $('#nvtMChannelZAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvSetPortMap') );//('[设置映射]');
                $('#nvtMChannelZAlertTxt').css({'color':'black'});
            }
        });
        $('#nvtMChannelZLan3Ckb').change(()=>{
            let rc = this.checkSlotMappingRule('LAN3', 'LTE-Z');
            if(true == rc){
                $('#nvtMChannelZLan3Ckb').prop('checked', true);
                $('#nvtMChannelZAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvPortMapLAN32ZErr')); //'[端口映射]表中已经建立LAN3<->LTE-Z高优先级映射!'
                $('#nvtMChannelZAlertTxt').css({'color':'red'});
            }
            else{
                // $('#nvtMChannelZLan3Ckb').prop('checked', false);
                $('#nvtMChannelZAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvSetPortMap') );//('[设置映射]');
                $('#nvtMChannelZAlertTxt').css({'color':'black'});
            }
        });
        // LTE-4G 配置
        $('#nvtMChannel4GLan1Ckb').change(()=>{
            let rc = this.checkSlotMappingRule('LAN1', 'LTE-4G');
            if(true == rc){
                $('#nvtMChannel4GLan1Ckb').prop('checked', true);
                $('#nvtMChannel4GAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvPortMapLAN12GErr')); //'[端口映射]表中已经建立LAN1<->LTE-4G高优先级映射!'
                $('#nvtMChannel4GAlertTxt').css({'color':'red'});
            }
            else{
                // $('#nvtMChannel4GLan1Ckb').prop('checked', false);
                $('#nvtMChannel4GAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvSetPortMap') );//('[设置映射]');
                $('#nvtMChannel4GAlertTxt').css({'color':'black'});
            }
        });
        $('#nvtMChannel4GLan2Ckb').change(()=>{
            let rc = this.checkSlotMappingRule('LAN2', 'LTE-4G');
            if(true == rc){
                $('#nvtMChannel4GLan2Ckb').prop('checked', true);
                $('#nvtMChannel4GAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvPortMapLAN22GErr')); //'[端口映射]表中已经建立LAN2<->LTE-4G高优先级映射!'
                $('#nvtMChannel4GAlertTxt').css({'color':'red'});
            }
            else{
                // $('#nvtMChannel4GLan2Ckb').prop('checked', false);
                $('#nvtMChannel4GAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvSetPortMap') );//('[设置映射]');
                $('#nvtMChannel4GAlertTxt').css({'color':'black'});
            }
        });
        $('#nvtMChannel4GLan3Ckb').change(()=>{
            let rc = this.checkSlotMappingRule('LAN3', 'LTE-4G');
            if(true == rc){
                $('#nvtMChannel4GLan3Ckb').prop('checked', true);
                $('#nvtMChannel4GAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvPortMapLAN32GErr')); //'[端口映射]表中已经建立LAN3<->LTE-4G高优先级映射!'
                $('#nvtMChannel4GAlertTxt').css({'color':'red'});
            }
            else{
                // $('#nvtMChannel4GLan3Ckb').prop('checked', false);
                $('#nvtMChannel4GAlertTxt').text(yutls.jsSwitchLang(this._oLanguage, 'nvSetPortMap')); //'[设置映射]'
                $('#nvtMChannel4GAlertTxt').css({'color':'black'});
            }
        });
        //-------- 注册button回调 ---------
        // porting mapping
        $('#netvTabMappingPortTblNewBtn').click(()=>{
            // get line index of table
            let tab = document.getElementById("netvTabMappingPortTbl") ;
            let newLineIndex = tab.rows.length - 3; //因为jquery在清除表格时留下一行空tr，所以要多减1
            let tr = "<tr> <td><input type='checkbox' /></td>" +
                    "<td><select class='netvTabMappingPortTblWlanSelector' style='width:70px'><option value='LTE-4G'>LTE-4G</option><option value='LTE-Z'>LTE-Z</option></select>" +
                    "    <a>:</a><input class='netvTabMappingPortTblWlanPortVal' placeholder='10000 - 20000' style='width:90px'/></td> " +
                    "<td><select class='netvTabMappingPortTblLanSelector' style='width:100px' id='netvTabMappingPortTblLanSelector"+newLineIndex+"' ></select>" +
                    "    <a>:</a><input class='netvTabMappingPortTblLanPortVal' style='width:70px'/></td>" +
                    "<td><a style='width:30px' class='netvTabMappingPortTblLanSlot' id='netvTabMappingPortTblLanSlot"+newLineIndex+"'></a></td>" +
                    "<td><input class='netvTabMappingMemoVal' style='width:200px'/></td> </tr> "
            $("#netvTabMappingPortTbl").append(tr);
            // insert LAN:IP list
            theApp.m_hMainWnd.oStore.LAN.accessList.forEach((element, index)=>{ // create lan:ip select->options
                let lanOptionStr = "<option value='"+element.IP+"'>"+element.IP+"</option>";
                $('#netvTabMappingPortTblLanSelector'+newLineIndex).append(lanOptionStr);
            });
            //
            //取值变化时进行hightlight => red 处理
            $('.netvTabMappingPortTblWlanSelector').change(this.netvTabMappingPortTblWlanSelectorOnChange);
            $('.netvTabMappingPortTblLanSelector').change(this.netvTabMappingPortTblLanSelectorOnChange);

            $('.netvTabMappingPortTblWlanPortVal').change(this.netvTabMappingPortTblXXPortValOnChange);
            $('.netvTabMappingPortTblLanPortVal').change(this.netvTabMappingPortTblXXPortValOnChange);
        });
        $('#netvTabMappingPortTblDelBtn').click(()=>{
            yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'nvDelLinesWarning'));//alert('所有选中的行将被删除！'); //TODO:是否发给服务器再删除？
            // get the selected idx file information
            $(":checkbox:checked","#netvTabMappingPortTbl").each(function(){
                // alert('所有选中的行都会被删除！');
                let idx = $(this).parents("tr").index();  // 获取checkbox所在行的顺序
                $("#netvTabMappingPortTbl").find("tr:eq("+ idx +")").remove();
            });
        });
        $('#netvTabMappingPortTblApplyBtn').click(()=>{
            var parametersErr = '';
            var selectedData = [];
            $("#netvTabMappingPortTbl").find("tr").each((idx, item)=>{
                // var wlan =  $('#netvTabMappingPortTblWlanSelector').children('option:selected').val();
                let wlan = $(item).find(".netvTabMappingPortTblWlanSelector").children('option:selected').val();
                let wlanPort = $(item).find('.netvTabMappingPortTblWlanPortVal').val();
                let slot = $(item).find('.netvTabMappingPortTblLanSlot').text();
                let ip = $(item).find(".netvTabMappingPortTblLanSelector").children('option:selected').val();
                let lanPort = $(item).find('.netvTabMappingPortTblLanPortVal').val();
                if(undefined != wlanPort){
                    if('' == wlanPort || (parseInt(wlanPort)<10000) ||(parseInt(wlanPort) > 20000)){
                        // alert("WLAN Port不能为空!");
                        parametersErr = yutls.jsSwitchLang(this._oLanguage, 'nvIcorrectWLanPortErr1')+ (idx-1) +yutls.jsSwitchLang(this._oLanguage, 'nvIcorrectWLanPortErr2');//"WLAN Port取值无效(无效行号="+ (idx-2) +"), 请确保取值范围[10000 - 20000]";
                        return false; // 用此方法退出jQuery each loop
                    }
                let cfg = {"WLanSlot": wlan,
                            "WLanPort": wlanPort,
                            "LanSlot": slot,
                            "LanIP": ip,
                            "LanPort": lanPort,
                            "Desc": $(item).find(".netvTabMappingMemoVal").val(),
                        };
                    selectedData.push(cfg);
                }
            });
            if('' != parametersErr){
                alert(parametersErr);
                return;
            }
            // "Mapping":{
            //     "portMapping":[{"WLanSlot":"LTE-4G", "WLanPort":"11000","LanSlot":"LAN1", "LanIP":"192.1.1.3", "LanPort":"110","Desc":"mock-xx-Max40字符"}, {...}]
            // }
            var str = {
                "command":"setPortMapping",
                "dat":{
                    "Mapping": {
                        "portMapping": selectedData
                    }
                }
            }
            let parameters = JSON.stringify(str);
            //console.log('Send=>'+parameters);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.Mapping.portMapping = selectedData;
                        // console.log(data.dat);
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        // nvtnvtMChannelMapApplyBtn
        $('#nvtnvtMChannelMapApplyBtn').click(()=>{
            var selectedData = {
                "slotLTEZ":{"LAN1":"", "LAN2":"", "LAN3":""},
                "slotLTE4G":{"LAN1":"", "LAN2":"", "LAN3":""}
            };
            $("#nvtMChannelZLan1Ckb").is(':checked') ? selectedData.slotLTEZ.LAN1 = 'on' : selectedData.slotLTEZ.LAN1 = 'off';
            $("#nvtMChannelZLan2Ckb").is(':checked') ? selectedData.slotLTEZ.LAN2 = 'on' : selectedData.slotLTEZ.LAN2 = 'off';
            $("#nvtMChannelZLan3Ckb").is(':checked') ? selectedData.slotLTEZ.LAN3 = 'on' : selectedData.slotLTEZ.LAN3 = 'off';
            $("#nvtMChannel4GLan1Ckb").is(':checked') ? selectedData.slotLTE4G.LAN1 = 'on' : selectedData.slotLTE4G.LAN1 = 'off';
            $("#nvtMChannel4GLan2Ckb").is(':checked') ? selectedData.slotLTE4G.LAN2 = 'on' : selectedData.slotLTE4G.LAN2 = 'off';
            $("#nvtMChannel4GLan3Ckb").is(':checked') ? selectedData.slotLTE4G.LAN3 = 'on' : selectedData.slotLTE4G.LAN3 = 'off';
            // "dat": {
            //   "Mapping":{ "slotLTEZ":{"LAN1":"on", "LAN2":"off", "LAN3":"on"},
            //               "slotLTE4G":{"LAN1":"off", "LAN2":"on", "LAN3":"off"},
            //      }
            //   }
            var str = {
                "command":"setSlotChannelMapping",
                "dat":{
                    "Mapping": selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.Mapping.slotLTEZ = selectedData.slotLTEZ;
                        theApp.m_hMainWnd.oStore.Mapping.slotLTE4G = selectedData.slotLTE4G;
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
        // netvTabMappMacIPNewBtn
        $('#netvTabMappMacIPNewBtn').click(()=>{
            let tr = "<tr> <td><input type='checkbox' /></td> <td><input class='netvTabMappingMacVal' value=''></td>" +
                    "<td><input class='netvTabMappingIPVal' type='IP' placeholder='10.10.10.11' value=''></td>" +
                    "<td><input class='netvTabMappingMacIpMemoVal' style='width:300px' value=''></td></tr>";
            $("#netvTabMappingMacIPTbl").append(tr);
        });
        // netvTabMappMacIPDelBtn
        $('#netvTabMappMacIPDelBtn').click(()=>{
            yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'nvDelLinesWarning'));//alert('所有选中的行将被删除！'); //TODO:是否发给服务器再删除？
            // get the selected idx file information
            $(":checkbox:checked","#netvTabMappingMacIPTbl").each(function(){
                // alert('所有选中的行都会被删除！');
                let idx = $(this).parents("tr").index();  // 获取checkbox所在行的顺序
                $("#netvTabMappingMacIPTbl").find("tr:eq("+ idx +")").remove();
            });
        });
        // netvTabMappMacIPApplyBtn
        $('#netvTabMappMacIPApplyBtn').click(()=>{
            var parametersErr = '';
            var selectedData = [];
            $("#netvTabMappingMacIPTbl").find("tr").each((idx, item)=>{
                var mac = $(item).find(".netvTabMappingMacVal").val();
                var ip = $(item).find(".netvTabMappingIPVal").val();
                // if(('' != mac) && (undefined != mac)){
                if((undefined != mac)&&(undefined != ip)){
                    if(('' == mac)||(false == yutls.chkMacAddress(mac))||
                        ('' == ip)||(false == yutls.chkIpAddress(ip))){
                        // alert("WLAN Port不能为空!");
                        parametersErr = yutls.jsSwitchLang(this._oLanguage, 'nvIcorrectValErr')+ (idx-1) +")";//"取值无效(无效行号="+ (idx-2) +")";
                        return false; // 用此方法退出jQuery each loop
                    }
                    let cfg = {"MAC": mac,
                        "IP": ip,
                        "Desc": $(item).find(".netvTabMappingMacIpMemoVal").val(),
                    };
                    selectedData.push(cfg);
                }
            });
            if('' != parametersErr){
                alert(parametersErr);
                return;
            }
            // 
            var str = {
                "command":"setMacIPMapping",
                "dat":{
                    "Mapping": {
                        "mac2ip": selectedData
                    }
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.Mapping.mac2ip = selectedData;
                        console.log(data.dat);
                        yutls.msgBox(yutls.jsSwitchLang(this._oLanguage, 'operationSucc')+ ' code:' + data.errCode); //'操作成功!
                    }
                    else{
                        yutls.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { yutls.msgBoxFailed(errorThrown);}
            });
        });
    }
    // evActived
    myActived(self) {
        let _this = self;
        // 接入设备表
        $("#netvTabLanAccessListTbl tr:gt(1)").remove();//empty(""); //1.清除表格内容
        theApp.m_hMainWnd.oStore.LAN.accessList.forEach(function(value,index){ //2.构建表格内容
            let number = index + 1;
            let tr = "<tr>"+
                    " <td><a>" + number + "</a></td> <td><a>"+value.port+"</a></td> <td><a>"+ value.MAC + "</a></td> <td><a>" + value.IP + "</a></td> <td><a>" + value.type + "</a></td></tr>";
            $("#netvTabLanAccessListTbl").append(tr);
        })
        
        // 端口映射
        if((undefined != theApp.m_hMainWnd.oStore.Mapping.portMapping) && (0 != theApp.m_hMainWnd.oStore.Mapping.portMapping.length)){
            $("#netvTabMappingPortTbl tr:gt(1)").remove();//empty("");
            // "portMapping":[{"WLanSlot":"LTE-Z", "WLanPort":"12000","LanSlot":"LAN3", "LanIP":"10.2.1.3", "LanPort":"80","Desc":"mock-xx-Max40字符"},
            theApp.m_hMainWnd.oStore.Mapping.portMapping.forEach(function(value,index){
                let wlanOptionStr = '';
                ('LTE-4G' == value.WLanSlot)? wlanOptionStr = "<option value='LTE-4G' selected >LTE-4G</option><option value='LTE-Z'>LTE-Z</option>" : wlanOptionStr = "<option value='LTE-4G' >LTE-4G</option><option value='LTE-Z' selected>LTE-Z</option>" ;
                let tr = "<tr> <td><input type='checkbox' /></td>" +
                        "<td><select style='width:70px' class='netvTabMappingPortTblWlanSelector'>" + wlanOptionStr + "</select>" +
                        "    <a>:</a><input class='netvTabMappingPortTblWlanPortVal' value='" + value.WLanPort + "' placeholder='10000 - 20000' style='width:90px'/></td> " +
                        "<td><select style='width:100px' class='netvTabMappingPortTblLanSelector' id='netvTabMappingPortTblLanSelector"+index+"' ></select>" +
                        "    <a>:</a><input class='netvTabMappingPortTblLanPortVal' value='"+ value.LanPort +"' style='width:70px'/></td>" +
                        "<td><a style='width:30px' class='netvTabMappingPortTblLanSlot' id='netvTabMappingPortTblLanSlot"+index+"'>"+ value.LanSlot +"</a></td>" +
                        "<td><input class='netvTabMappingMemoVal' value='"+ value.Desc +"' style='width:200px'/></td> </tr> "
                $("#netvTabMappingPortTbl").append(tr);
                let trIndex = index;
                // insert LAN:IP list
                theApp.m_hMainWnd.oStore.LAN.accessList.forEach((element, index)=>{ // create lan:ip select->options
                    let lanOptionStr = ''; //<option value=128.0.1.22 selected>128.0.1.22</option>
                    if(value.LanIP == element.IP){
                        lanOptionStr = "<option value='"+element.IP+"' selected>"+element.IP+"</option>";
                    }
                    else{
                        lanOptionStr = "<option value='"+element.IP+"'>"+element.IP+"</option>";
                    }
                    $('#netvTabMappingPortTblLanSelector'+trIndex).append(lanOptionStr);
                });
            })
            //regist select callback.
            //取值变化时进行hightlight => red 处理
            $('.netvTabMappingPortTblWlanSelector').change(this.netvTabMappingPortTblWlanSelectorOnChange);
            $('.netvTabMappingPortTblLanSelector').change(this.netvTabMappingPortTblLanSelectorOnChange);

            $('.netvTabMappingPortTblWlanPortVal').change(this.netvTabMappingPortTblXXPortValOnChange);
            $('.netvTabMappingPortTblLanPortVal').change(this.netvTabMappingPortTblXXPortValOnChange);
        }
        //
        // 通道映射
        // "slotLTEZ":{"LAN1":"on", "LAN2":"off", "LAN3":"on"},
        // "slotLTE4G":{"LAN1":"on", "LAN2":"off", "LAN3":"on"},
        var changedFlg = false;
        var lan1Z, lan2Z, lan3Z, lan14G, lan24G, lan34G;
        //--slotLTEZ
        if((('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN1)&&(true == _this.checkSlotMappingRule('LAN1', 'LTE-Z')))
            ||(('on' != theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN1)&&(false == _this.checkSlotMappingRule('LAN1', 'LTE-Z')))){
            //
            changedFlg = false;
        }
        else{
            changedFlg = true;
            if('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN1){
                theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN1 = 'off';
            }
            else{
                theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN1 = 'on';
            }
        }
        ('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN1)? lan1Z = true : lan1Z = false;
        //
        if((('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN2)&&(true == _this.checkSlotMappingRule('LAN2', 'LTE-Z')))
            ||(('on' != theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN2)&&(false == _this.checkSlotMappingRule('LAN2', 'LTE-Z')))){
            //
            changedFlg = false;
        }
        else{
            changedFlg = true;
            if('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN2){
                theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN2 = 'off';
            }
            else{
                theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN2 = 'on';
            }
        }
        ('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN2)? lan2Z = true : lan2Z = false;
        //
        if((('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN3)&&(true == _this.checkSlotMappingRule('LAN3', 'LTE-Z')))
            ||(('on' != theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN3)&&(false == _this.checkSlotMappingRule('LAN3', 'LTE-Z')))){
            //
            changedFlg = false;
        }
        else{
            changedFlg = true;
            if('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN3){
                theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN3 = 'off';
            }
            else{
                theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN3 = 'on';
            }
        }
        ('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTEZ.LAN3)? lan3Z = true : lan3Z = false;
        //--slotLTE4G
        if((('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN1)&&(true == _this.checkSlotMappingRule('LAN1', 'LTE-4G')))
            ||(('on' != theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN1)&&(false == _this.checkSlotMappingRule('LAN1', 'LTE-4G')))){
            //
            changedFlg = false;
        }
        else{
            changedFlg = true;
            if('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN1){
                theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN1 = 'off';
            }
            else{
                theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN1 = 'on';
            }
        }
        ('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN1)? lan14G = true : lan14G = false;
        //
        if((('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN2)&&(true == _this.checkSlotMappingRule('LAN2', 'LTE-4G')))
            ||(('on' != theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN2)&&(false == _this.checkSlotMappingRule('LAN2', 'LTE-4G')))){
            changedFlg = false;
        }
        else{
            changedFlg = true;
            if('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN2){
                theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN2 = 'off';
            }
            else{
                theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN2 = 'on';
            }
        }
        ('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN2)? lan24G = true : lan24G = false;
        //
        if((('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN3)&&(true == _this.checkSlotMappingRule('LAN3', 'LTE-4G')))
            ||(('on' != theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN3)&&(false == _this.checkSlotMappingRule('LAN3', 'LTE-4G')))){
            changedFlg = false;
        }
        else{
            changedFlg = true;
            if('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN3){
                theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN3 = 'off';
            }
            else{
                theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN3 = 'on';
            }
        }
        ('on' == theApp.m_hMainWnd.oStore.Mapping.slotLTE4G.LAN3)? lan34G = true : lan34G = false;
        $("#netvTabMappingSlotTbl").find("tr").each(function(idx, item){
            $(item).find("#nvtMChannelZLan1Ckb").attr("checked",lan1Z);
            $(item).find("#nvtMChannelZLan2Ckb").attr("checked",lan2Z);
            $(item).find("#nvtMChannelZLan3Ckb").attr("checked",lan3Z);
            $(item).find("#nvtMChannel4GLan1Ckb").attr("checked",lan14G);
            $(item).find("#nvtMChannel4GLan2Ckb").attr("checked",lan24G);
            $(item).find("#nvtMChannel4GLan3Ckb").attr("checked",lan34G);
        });
        //如端口映射和通道映射有不一致，发生了自动修正操作，将结果上送服务器保存
        if(true == changedFlg){
            // "dat": {
            //   "Mapping":{ "slotLTEZ":{"LAN1":"on", "LAN2":"off", "LAN3":"on"},
            //               "slotLTE4G":{"LAN1":"off", "LAN2":"on", "LAN3":"off"},
            //   }
            var selectedData = {
                "slotLTEZ" :theApp.m_hMainWnd.oStore.Mapping.slotLTEZ,
                "slotLTE4G":theApp.m_hMainWnd.oStore.Mapping.slotLTE4G
            };
            var str = {
                "command":"setSlotChannelMapping",
                "dat":{
                    "Mapping": selectedData
                }
            }
            let parameters = JSON.stringify(str);
            $.ajax({
                type:"POST",
                url: yutls.getDebugURL(), //"/cgi-bin/cgi.cgi",
                data:parameters,
                contentType: "application/json",
                dataType: 'json',
                success: (res)=>{
                    // 返回参数格式
                    // { "rc": 0/1, "errCode": "xxx" }
                    //var data = JSON.parse(res);
                    var data = res;
                    console.log(data);
                    if(0 == data.rc){
                        theApp.m_hMainWnd.oStore.Mapping.slotLTEZ = selectedData.slotLTEZ;
                        theApp.m_hMainWnd.oStore.Mapping.slotLTE4G = selectedData.slotLTE4G;
                    }
                    else{
                        tools.msgBox(tools.jsSwitchLang(enJsMap, cnJsMap, 'operationSucc') + '  Code:' +data.errCode);//操作成功
                        tools.msgBox(data.errCode);
                    }
                },
                error: function (errorThrown) { tools.msgBoxFailed(errorThrown);}
            });
        }

        // IP/MAC绑定
        if((undefined != theApp.m_hMainWnd.oStore.Mapping.mac2ip) && (0 != theApp.m_hMainWnd.oStore.Mapping.mac2ip.length)){
            $("#netvTabMappingMacIPTbl tr:gt(1)").remove();//empty("");
            // "macVSip":[{"MAC":"01-29-03", "IP":"123.4.3.1","Desc":"xx-Max40字符"},
            //             {"MAC":"01-29-04", "IP":"123.4.3.2","Desc":"xx-Max40字符"}]
            theApp.m_hMainWnd.oStore.Mapping.mac2ip.forEach(function(value,index){
                let tr = "<tr> <td><input type='checkbox' /></td> <td><input class='netvTabMappingMacVal' value=" + value.MAC + "></td>" +
                        "<td><input class='netvTabMappingIPVal' type='IP' placeholder='10.10.10.11' value="+ value.IP+"></td>" +
                        "<td><input class='netvTabMappingMacIpMemoVal' style='width:300px' value="+ value.Desc+"></td></tr>";
                $("#netvTabMappingMacIPTbl").append(tr);
            })
            }
    }
    //-- internal functions
    //
    //即时检查是否符合映射规则
    checkSlotMappingRule(aLan, aWlan){
        var rc = false; // false => 两者没有映射关系
        for(let i = 0; i < theApp.m_hMainWnd.oStore.Mapping.portMapping.length; i++){
            // console.log(theApp.m_hMainWnd.oStore.Mapping.portMapping[i].WLanSlot+'<>'+theApp.m_hMainWnd.oStore.Mapping.portMapping[i].LanSlot);
            if((theApp.m_hMainWnd.oStore.Mapping.portMapping[i].WLanSlot == aWlan)&&(theApp.m_hMainWnd.oStore.Mapping.portMapping[i].LanSlot == aLan)){
                // console.log(theApp.m_hMainWnd.oStore.Mapping.portMapping[i].WLanSlot+'<>'+theApp.m_hMainWnd.oStore.Mapping.portMapping[i].LanSlot);
                rc = true; //有映射关系
                break;
            }
        }
        // console.log('checkSlotMappingRule.rc='+rc);
        return rc;
    }

    //取值变化时进行hightlight => red 处理
    netvTabMappingPortTblLanSelectorOnChange(node){
        let ipVal = $(this).children('option:selected').val();
        let selectID = $(this).attr("id");
        let ipID = '#netvTabMappingPortTblLanSlot' + selectID.replace("netvTabMappingPortTblLanSelector","");
        theApp.m_hMainWnd.oStore.LAN.accessList.forEach((element, index)=>{ // create lan:ip select->options
            if(ipVal == element.IP){
                $(ipID).text(element.port);
                $(ipID).css({"color": "red"});
            }
            // console.error('No SLot is found!');
        });
    }
    netvTabMappingPortTblWlanSelectorOnChange(node){
        $(this).css({"color": "red"});
    }
    netvTabMappingPortTblXXPortValOnChange(node){
        $(this).css({"color": "red"});
    }
    netvTabMappingPortTblWlanSelectorOnChange(node){
        $(this).css({"color": "red"});
    }
    netvTabMappingPortTblXXPortValOnChange(node){
        $(this).css({"color": "red"});
    }
}
