import htmlTemplate from './CNetworkView.html';
import $ from 'jquery';
import {yutls, theApp, CView} from 'yut';
import CNetvTabGnss from './subviews/CNetvTabGnss';
import CNetvTabLan from './subviews/CNetvTabLan';
import CNetvTabRTMP from './subviews/CNetvTabRTMP';
import CNetvTabVpn from './subviews/CNetvTabVpn';
import CNetvTabRouting from './subviews/CNetvTabRouting';
import CNetvTabFWFilter from './subviews/CNetvTabFWFilter';
import CNetvTabMapping from './subviews/CNetvTabMapping';
/**
 * CNetworkView View的主Class定义
 * */
class CNetworkView extends CView{
    constructor(hParent){
        // supper call before user 'this' pointer.
        super();
        var _this = this;
        //1. internal variant / function
        _this.Data = {
            
        }
        // 多语言实现 => 移到Class最后避免影响阅读
        //init option=> aNodeID [Peremptory], aHtml, aRegCtrlCallBack, aLanguage
        let option = {
                aParent: hParent,
                aNodeID: 'networkView',
                aHtml: htmlTemplate, //'./src/views/CNetworkView.html',
                aRegCtrlCallBack: this.regCtrlCb,
                aLanguage: { language: 'cn', 
                            htmlMap: {'cn': _this.cnHtmlMap ,'en': _this.enHtmlMap,},
                            jsMap: {'cn': _this.cnJsMap,'en': _this.enJsMap,} },
                aRenderData: _this.Data,
                aChildViews: [CNetvTabGnss, CNetvTabLan, CNetvTabRTMP, CNetvTabVpn, CNetvTabRouting, CNetvTabFWFilter, CNetvTabMapping,],
                aEvCallback: {evActived: _this.evMyActived},//{evMounted: _this.myMounted,},
            };
        // init this class-object
        _this.init(option);
        //2. exported variant / function
    }
    //
    regCtrlCb(){
        //注册控件回调函数
        //init: tab control
        var parentViewObj = this;
        $('#netvTab').delegate(".tab-header-item", "click", function(){
            //set this tab to ".tab-header-selected",others to none
            $(".tab-header .tab-header-item").siblings().removeClass("tab-header-selected");
            var selected = $(this)
            selected.addClass("tab-header-selected");
            var targetid = selected.attr("target");
            //get the "target"
            $(".tab-content .tab-content-view").siblings().addClass("hide");
            $("#" + targetid).removeClass("hide");
            //show the tab subview
            parentViewObj.activeView(targetid);
        });

    }
    // evActived
    evMyActived(self) {
        let _this = self;
        // get networkcfg
        theApp.m_hMainWnd.oStore.getNetworkCfgInfor();
        // render
        // _this.render();
    }
    render(){
        // var enJsMap = this.enJsMap;
        // var cnJsMap = this.cnJsMap;
        //获取当前active的TabView
        // var tabID = 0;
        // tabID = $('#netvTab .tab-header .tab-header-item').siblings('.tab-header-selected').attr('target');
        // if(0 == tabID)
        //     tabID = 'netvTabLan';
        // console.log(tabID);

    }

}
